<template>
  <transition
    appear
    :duration="10000"
  >
    <div
      class="e-transaction"
      :class="[
        buyOrSell === BUY_TOKEN_ORDER ? 'e-transaction_buy' : 'e-transaction_sell',
        { 'is-my': isMy },
      ]"
      @click="$emit('click', $event)"
    >
      <div class="e-transaction__bg"></div>
      <div
        class="e-transaction__col e-transaction__name"
        :class="buyOrSell === BUY_TOKEN_ORDER ? 'e-transaction__name_buy' : 'e-transaction__name_sell'"
      >
        <e-big-number :value="value" :decimals="decimals" :decimal-places="3"/>
      </div>

      <div class="e-transaction__col e-transaction__detail">
        <e-market-name :exchange="exchange"/>

        <div class="e-transaction__name-icon"><e-icon :src="buyOrSell === BUY_TOKEN_ORDER ? iconBuy : iconSell"/></div>

        <e-big-number :value="price" :decimal-places="9"/>
      </div>

      <div
        class="e-transaction__col e-transaction__time e-transaction__right-align"
        :class="confirmationStateClass"
      >
        <div class="e-transaction__time-text">{{ formattedDate }}</div>
        <!--<div class="e-transaction__time-status">-->
          <!--<div class="e-transaction__hint">{{ confirmationStateLabel }}</div>-->
        <!--</div>-->
      </div>

      <div class="e-transaction__col e-transaction__result e-transaction__right-align">
        <e-big-number :value="ethValue" :decimals="ETH_DECIMALS" :decimal-places="3"/>
        <div class="e-transaction__result-icon"><e-icon :src="iconEth"/></div>
      </div>
    </div>
  </transition>
</template>

<script>

/**
 * Component:     TheTrades
 *
 * @version 1.0.2
 */

import { BigNumber } from 'bignumber.js';
import { tradesDate } from '@/util/dates';
import { APPROVED_STATUS, PENDING_STATUS, DECLINED_STATUS, BUY_TOKEN_ORDER, EXCHANGE_ETHERMIUM } from '@/util/constants';

// Components
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import EMarketName from '@/components/EMarketName';

// Icons
import iconSell from '@/icons/e-icon_loss.svg';
import iconBuy from '@/icons/e-icon_profit.svg';
import iconEth from '@/icons/e-icon_earned.svg';

const confirmationStateClasses = {
  [PENDING_STATUS]: 'e-transaction__time_pending',
  [APPROVED_STATUS]: 'e-transaction__time_confirmed',
  [DECLINED_STATUS]: 'e-transaction__time_declined',
};

const confirmationStateLabels = {
  [PENDING_STATUS]: 'Pending',
  [APPROVED_STATUS]: 'Confirmed',
  [DECLINED_STATUS]: 'Declined',
};

export default {
  name: 'e-transaction',

  data() {
    return {
      iconEth,
      iconBuy,
      iconSell,
    };
  },

  props: {
    //   (/)
    buyOrSell: {
      type: String,
      default: BUY_TOKEN_ORDER,
    },

    //  
    value: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //  
    ethValue: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //      
    price: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //     
    time: {
      type: Date,
      default: null,
    },

    // 
    exchange: {
      type: String,
      default: EXCHANGE_ETHERMIUM,
    },

    confirmationState: {
      type: String,
      default: PENDING_STATUS,
      validator: v => [PENDING_STATUS, APPROVED_STATUS, DECLINED_STATUS].includes(v),
    },

    isMy: {
      type: Boolean,
      default: false,
    },

    //      
    decimals: {
      type: Number,
      default: 0,
    },
  },

  computed: {
    confirmationStateClass() {
      return confirmationStateClasses[this.confirmationState];
    },

    confirmationStateLabel() {
      return confirmationStateLabels[this.confirmationState];
    },

    formattedDate() {
      return this.time !== null
        ? tradesDate(this.time)
        : '';
    },
  },

  components: {
    EIcon,
    EBigNumber,
    EMarketName,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-transaction {
  $this: &;

  border-radius: 2px;
  display: flex;
  padding: 5px 11px 1px 10px;
  box-shadow: 0 1px 3px 0 rgba(0, 0, 0, 0.08);
  min-height: 26px;
  box-sizing: border-box;
  transform: translate3d(0, 0, 0);
  position: relative;
  z-index: 1;
  margin-bottom: 1px;

  &.is-my {
    cursor: pointer;

    &:hover {
      &:before {
        background-color: #f3f6ff;
      }
    }
  }

  &:before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
    border-radius: 2px;
    background-color: #fff;
    // transition: $transition-time;
    transition: 1s;
  }

  &__bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    border-radius: 2px;
    opacity: 0;
  }

  &.e-transaction_sell &__bg {
    background-color: $color-red;
  }

  &.e-transaction_buy &__bg {
    background-color: $color-green;
  }

  @include rmin(1921) {
    flex-wrap: wrap;
    padding: 7px 10px 7px;
  }

  @include rmin(2400) {
    padding: 14px 17px 17px;
  }

  @include r(1600) {
    flex-wrap: wrap;
    padding: 9px 9px 9px 10px;
  }

  &__col {
    white-space: nowrap;
  }

  &__market-name {
    display: inline-block;
    font-size: 11px;
    text-transform: uppercase;
    font-weight: 800;
    color: #0D105B;
    border: 1px solid #d9d9e2;
    border-radius: 4px;
    padding: 0 2px;
    line-height: 15px;
    margin-right: 2px;
    transition: border-color .15s ease-in-out, color .15s ease-in-out;

    @include rmin(1921) {
      margin-right: 5px;
    }

    &:hover {
      border-color: #0e30e2;
      color: #0e30e2;
    }
  }

  &__time {
    align-items: center;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-end;
    order: 2;
    flex-basis: 24%;
    max-width: 24%;
    min-width: 24%;
    padding-top: 2px;
    padding-right: 2px;

    @include r(1500) {
      padding-top: 3px;
      order: 4;
      flex-basis: 50%;
      max-width: 50%;
      min-width: 50%;
    }

    @include r(1600) {
      padding-top: 3px;
      order: 4;
    }

    @include rmin(1501) {
      order: 4;
      flex-basis: 31%;
      max-width: 31%;
      min-width: 31%;
    }

    @include rmin(1921) {
      order: 4;
      flex-basis: 35%;
      max-width: 35%;
      min-width: 35%;
    }

    @include rmin(2400) {
      flex-basis: 220px;
      max-width: 220px;
      min-width: 220px;
      margin-left: auto;
    }
  }

  &__time-text {
    position: relative;
    font-size: 11px;
    z-index: 2;
    font-weight: 400;
    transition: 0.3s;
    white-space: nowrap;
    color: $color-grey;

    &:hover {
      #{$this}__hint {
        visibility: visible;
        opacity: 1;
      }
    }

    @include rmin(1921) {
      font-size: 14px;
    }

    @include rmin(2400) {
      font-size: 11px;
      padding-top: 2px;
    }
  }

  &__time-status {
    border: 2px solid;
    border-radius: 50%;
    min-height: 8px;
    margin-left: 4px;
    margin-top: -2.5px;
    min-width: 8px;
    position: relative;
    z-index: 2;

    &:hover {
      #{$this}__hint {
        visibility: visible;
        opacity: 1;
      }
    }
  }

  &__time_pending {
    #{$this}__time-status {
      background-color: lighten($color-orange, 38%);
      border-color: $color-orange;
    }
  }

  &__time_confirmed {
    #{$this}__time-status {
      background-color: lighten($color-green-bright, 38%);
      border-color: $color-green-bright;
    }
  }

  &__time_declined {
    #{$this}__time-status {
      background-color: lighten($color-red, 38%);
      border-color: $color-red;
    }
  }

  &__icon {
    position: absolute;
    right: -18px;
    top: 0;
    width: 13px;
    height: 13px;

    @include rmin(1921) {
      height: 18px;
      width: 18px;
      right: -26px;
      top: 2px;
    }

    @include rmin(2400) {
      height: 18px;
      width: 18px;
      right: -21px;
      top: 2px;
    }

    @include r(1600) {
      top: 0;
      right: -19px;
    }
  }

  &__name {
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    transition: 0.3s;
    position: relative;
    padding-left: 0;
    box-sizing: border-box;
    order: 1;
    color: #09168A;
    flex-basis: 33%;
    max-width: 33%;

    @include r(1500) {
      padding-left: 0;
      flex-basis: 50%;
      max-width: 50%;
      min-width: 50%;
    }

    @include rmin(1501) {
      order: 2;
      text-align: right;
      flex-basis: 17%;
      max-width: 17%;
    }

    @include rmin(1921) {
      order: 2;
      text-align: right;
      flex-basis: 14%;
      max-width: 14%;
    }

    @include rmin(2400) {
      margin-left: auto;
    }

    .e-big-number {
      &__fraction {
        opacity: 0.6;
        letter-spacing: 0.8px;
        margin-left: 1px;

        @include rmin(2400) {
          letter-spacing: 0.93px;
        }
      }
    }
  }

  &__name_sell {
    .e-big-number {
      // color: $color-red;
    }

    #{$this}__shortname {
      // color: $color-red;
      // opacity: 0.5;
    }
  }

  &__name_buy {
    .e-big-number {
      // color: $color-green-bright;
    }

    #{$this}__shortname {
      // color: $color-green;
      // opacity: 0.5;
    }
  }

  &__name-icon {
    display: inline-block;
    position: relative;
    height: 11px;
    width: 12px;
    top: 2px;
    left: 0;

    @include rmin(2400) {
      top: 2px;
      left: 0;
      width: 18px;
      height: 16px;
    }

    @include rmin(1921) {
      top: 2px;
      left: 0;
      width: 18px;
      height: 16px;
    }
  }

  &__detail {
    color: $color-ultramarine;
    letter-spacing: 0.8px;
    font-size: 12px;
    font-weight: 400;
    text-transform: uppercase;
    transition: 0.3s;
    box-sizing: border-box;
    order: 2;
    flex-basis: 35%;
    max-width: 35%;
    min-width: 35%;

    .e-big-number {
      &__fraction {
        letter-spacing: 0.8px;
      }

      &__leading-zeros {
        opacity: 0.5;
      }
    }

    @include r(1500) {
      font-size: 11px;
      padding-top: 3px;
      padding-left: 0;
      order: 3;
      flex-basis: 50%;
      max-width: 50%;
      min-width: 50%;
    }

    @include rmin(1501) {
      order: 1;
    }

    @include rmin(1921) {
      flex-basis: 38%;
      max-width: 38%;
      min-width: 38%;
    }
  }

  &__detail-icon {
    height: 10px;
    width: 10px;
    display: inline-block;
    position: relative;
    top: 1px;
    left: -3px;

    .e-icon svg {
      fill: #cfcfcd;
    }

    @include rmin(1921) {
      height: 12px;
      width: 12px;
    }

    &_decrease {
      .e-icon svg {
        fill: $color-red;
      }
    }

    &_increase {
      .e-icon svg {
        fill: $color-green-bright;
      }
    }
  }

  &__result {
    color: $color-blue;
    letter-spacing: 0.8px;
    font-size: 12px;
    font-weight: 600;
    text-transform: uppercase;
    position: relative;
    box-sizing: border-box;
    order: 3;
    flex-basis: 23%;
    max-width: 23%;
    padding-right: 9px;

    @include r(1500) {
      flex-basis: 50%;
      max-width: 50%;
      min-width: 50%;
      order: 1;
    }

    @include rmin(1501) {
      flex-basis: 17%;
      max-width: 17%;
    }

    @include rmin(1921) {
      flex-basis: 13%;
      max-width: 13%;
    }


    @include rmin(2400) {
      margin-left: auto;
    }
  }

  &__result-icon {
    width: 10px;
    height: 10px;
    display: inline-block;
    position: absolute;
    top: 4px;
    right: -1px;

    .e-icon svg {
      fill: $color-semi-dark-blue;
    }

    @include rmin(1921) {
      height: 12px;
      width: 12px;
      top: 3px;
      right: -3px;
    }
  }

  &__right-align {
    text-align: right;
  }

  &__hint {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: 0.3s;
    top: -7px;
    right: 12px;
    background-color: #fff;
    color: #0D105B;
    font-size: 12px;
    font-weight: 400;
    line-height: 1;
    box-shadow: 0 3px 10px rgba(148, 165, 180, 0.14);
    border-radius: 3px;
    padding: 5px 10px 5px;
    white-space: nowrap;
  }
}
</style>




