<template>
  <div
    class="the-tokens"
    :class="{ 'the-tokens_big': big }"
  >
    <div class="the-tokens__filter">
      <div class="the-tokens__filters">
        <div class="the-tokens__col">
          <div class="the-tokens__btns">
            <e-button-tabs
              :tabs="tabs"
              :active-tab.sync="mode"
            />
          </div>
        </div>
        <div class="the-tokens__col">
          <div class="the-tokens__search">
            <e-token-search v-model="searchString" />
          </div>
        </div>
      </div>
    </div>

    <transition-content>
      <div
        class="the-tokens__container"
        :class="{
          'the-tokens__container_empty': tokensToDisplay.length === 0,
          'is-reach-end':  reachEnd,
        }"
      >
        <div
          v-if="tokensToDisplay.length > 0"
          class="the-tokens__scroll-area"
        >
          <div
          id="the-tokens-list"
          class="the-tokens__list"
          >
            <e-token
              v-for="item in tokensToDisplay"
              :key="item.contract"
              v-bind="item"
              @click="setToken(item.contract)"
              @toggle-my="toggleTokenFavorite(item.contract)"
            />
          </div>
        </div>

        <div
          v-else-if="showSignUpWarning"
          key="emptyWallet"
          class="the-tokens__alert"
        >
          <div class="the-tokens__alert-body">
            <div class="the-tokens__alert-circle">
              <div class="the-tokens__alert-icon">
                <e-icon :src="iconTrending"/>
              </div>
            </div>
            <div class="the-tokens__alert-title">
              Your tokens
            </div>
            <div class="the-tokens__alert-descr">
              Please sign up and add you favorite tokens
            </div>
          </div>
        </div>

        <div
          v-else
          key="noResults"
          class="the-tokens__error"
        >
          <the-token-no-result @clearSearchLine="clearSearchLine" />
        </div>

      </div>
    </transition-content>
    <div class="the-tokens__catalog-button">
      <router-link :to="$popupRoute(POPUP_TOKEN_CATALOGUE)">
        <e-button
          fill="white"
          :icon="iconTokenCatalog"
          iconMargin="primary"
          padding="spacing"
          size="s"
        >Token catalogue</e-button>
      </router-link>
    </div>
  </div>
</template>

<script>
/**
 *         
 *
 * @version 1.0.2
 */
import { mapActions } from 'vuex';

// Components
import EToken from '@/components/EToken';
import EIcon from '@/components/EIcon';
import ETokenSearch from '@/components/ETokenSearch';
import EButtonTabs from '@/components/EButtonTabs';
import EButton from '@/components/EButton';
import TheTokenNoResult from '@/containers/blocks/TheTokenNoResult';

// Transitions
import TransitionContent from '@/containers/transitions/TransitionContent';

// Icons
import iconTrending from '@/icons/e-icon_trading.svg';
import iconTokenCatalog from '@/icons/e-icon_token-catalog.svg';

import pick from '@/util/pick';
import { routesMixin, searchTokenMixin } from '@/util/mixins';

const MODE_TENDING = 'Trending';
const MODE_MY_TOKENS = 'Favorites';


export default {
  name: 'the-tokens',

  props: {
    big: {
      type: Boolean,
      default: false,
    },
  },

  data() {
    return {
      mode: MODE_TENDING,
      searchString: '',
      reachEnd: false,
    };
  },

  computed: {
    tokensToDisplay() {
      const tokens = (this.searchString === '' && this.mode === MODE_MY_TOKENS)
        ? this.myTokens
        : this.$searchTokens(this.searchString, this.allTokens);
      return tokens.slice(0, 20);
    },

    allTokens() {
      const activeTokenContract = this.$store.getters['tokens/activeToken'].contract;
      return this.$store.getters['tokens/sortedTokens'].map((t) => {
        const mapped = pick(t, [
          'candles24h',
          'contract',
          'decimals',
          'increasing24',
          'isMy',
          'isWebsiteVerified',
          'name',
          'price',
          'priceDiff24InPercent',
          'shortName',
          'total',
          'volume24h',
          'websiteUrl',
        ]);
        mapped.active = t.contract === activeTokenContract;
        return mapped;
      });
    },

    myTokens() {
      const activeTokenContract = this.$store.getters['tokens/activeToken'].contract;
      return this.$store.getters['tokens/myTokens'].map((t) => {
        const mapped = pick(t, [
          'candles24h',
          'contract',
          'decimals',
          'increasing24',
          'isMy',
          'isWebsiteVerified',
          'name',
          'price',
          'priceDiff24',
          'priceDiff24InPercent',
          'shortName',
          'total',
          'volume24h',
          'websiteUrl',
        ]);
        mapped.active = t.contract === activeTokenContract;
        return mapped;
      });
    },

    showSignUpWarning() {
      return !this.$store.getters['wallets/isLogin'] && this.mode === MODE_MY_TOKENS;
    },
  },

  watch: {
    mode() {
      this.reachEnd = false;
      this.searchString = '';
    },
  },

  created() {
    Object.assign(this, {
      iconTrending,
      iconTokenCatalog,
      tabs: [MODE_TENDING, MODE_MY_TOKENS],
    });
  },

  methods: {
    async setToken(token) {
      this.$emit('active-token-change');
      this.$router.push({ name: 'token', params: { token } });
    },

    ...mapActions({
      toggleTokenFavorite: 'tokens/toggleTokenFavorite',
    }),

    clearSearchLine() {
      this.searchString = '';
      this.mode = MODE_TENDING;
    },

    onReachEnd() {
      this.reachEnd = true;
    },

    onScrollUp() {
      if (this.reachEnd) {
        this.reachEnd = false;
      }
    },
  },

  components: {
    EButton,
    EButtonTabs,
    EIcon,
    EToken,
    ETokenSearch,
    TheTokenNoResult,
    TransitionContent,
  },
  mixins: [routesMixin, searchTokenMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-tokens {
  $this: &;
  $header-size: 50px;
  $button-size: 60px;

  position: relative;
  height: calc(100% - 18px);
  margin-top: 18px;
  user-select: none;

  &_big {
    margin-top: 14px;
  }

  &__container {
    height: calc(100% - #{$header-size} - 8px);
    position: relative;

    //    
    &:after {
      content: '';
      position: absolute;
      bottom: 50px;
      width: 100%;
      height: 98px;
      background-image: linear-gradient(180deg, rgba(#F4F5F9, 0) 0%, #F4F5F9 100%);
      z-index: 1;
      pointer-events: none;
      opacity: 1;
      transition: opacity 0.5s ease-in-out;
    }

    &.is-reach-end {
      &:after {
        opacity: 0;
      }
    }
  }

  &__container_empty {
    &:after {
      opacity: 0;
    }
  }

  &__scroll-area {
    overflow-y: auto;
    margin-top: 8px;
    position: relative;
    padding-right: 8px;
    box-sizing: border-box;
    height: calc(100% - #{$button-size});

    @include r(1200) {
      margin-top: 10px;
    }

    @include rmin(1921) {
      margin-top: 14px;
    }

    &::-webkit-scrollbar {
      width: 2px;
    }

    &::-webkit-scrollbar-track {
      background: $color-violet;
      border-radius: 2px;
    }

    &::-webkit-scrollbar-thumb {
      background: $color-blue;
      border-radius: 2px;
    }
  }

  .e-token {
    &:not(:last-of-type) {
      margin-bottom: 8px;
    }

    &:last-of-type {
      margin-bottom: 7px;
    }
  }

  &__alert {
    margin-top: 8px;
    padding-right: 9px;
    height: calc(100% - 60px);

    @include rmin(1921) {
      padding-right: 5px;
      margin-top: 14px;
    }
  }

  &__alert-body {
    border-radius: 3px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    background-color: #fff;
    padding: 20px;
    box-sizing: border-box;
    width: 100%;
    height: 100%;
  }

  &__alert-circle {
    height: 40px;
    width: 40px;
    border-radius: 50%;
    box-shadow: 0 2px 4px rgba(black, .14);
    background-color: #fff;
    margin-bottom: 10px;
    display: flex;
    justify-content: center;
    align-items: center;
  }

  &__alert-icon {
    width: 20px;
    height: 13px;
    margin-right: -4px;
  }

  &__alert-title {
    font-size: 18px;
    color: $color-blue;
    padding-bottom: 10px;
    margin-bottom: 15px;
    position: relative;
    display: inline-block;

    &:before {
      content: '';
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: 2px;
      background-color: $color-blue;
      border-radius: 100px;
    }
  }

  &__alert-descr {
    font-size: 15px;
    font-weight: 400;
    line-height: 1.5;
    color: $color-ultramarine;
    text-align: center;
  }

  &__error {
    margin-top: 8px;
    padding-right: 9px;

    @include rmin(1921) {
      padding-right: 5px;
      margin-top: 14px;
    }
  }

  &__filters {
    display: flex;
    transition: opacity 0.5s;
    will-change: opacity;
    flex-wrap: nowrap;
    justify-content: space-between;
  }

  &__search {
    height: 24px;
    margin-left: 6px;
    margin-right: 2px;

    @include r(1200) {
      width: 100px;
    }

    @include rmin(1921) {
      width: 100px;
    }

    @include r(1200) {
      height: 24px;
      font-size: 10px;
      padding-right: 37px;
    }
  }

  .search-box-enter-active,
  .search-box-leave-active {
    transition: 0.5s;
    transition-property: transform, opacity, width;
    will-change: transform, opacity, width;
  }

  .search-box-enter,
  .search-box-leave-to {
    opacity: 0;
    width: 107px;
  }

  &__catalog-button {
    position: absolute;
    bottom: 0;
    width: 100%;
    left: 0;
    text-align: center;

    @include r(1200) {
      bottom: 10px;
    }

    @include h(750) {
      bottom: 10px;
    }

    @include rmin(1921) {
      bottom: 20px;
    }
  }

  &__list {
    -webkit-tap-highlight-color: rgba(0,0,0,0);
  }

  &__list-wrapper {
    position: relative;

    &:after {
      content: '';
      position: absolute;
      bottom: 0;
      width: 100%;
      height: 98px;
      background-image: linear-gradient(180deg, rgba(#F4F5F9, 0) 0%, #F4F5F9 100%);
      z-index: 1;
      pointer-events: none;
    }
  }
}
</style>



