<template>
  <div>
    <div class="page-charts">
      <div
        class="page-charts__wrapp"
      >
        <!-- header menu -->
        <the-header-inner/>
        <!-- header menu -->

        <div class="page-charts__wrapp-content">
          <div class="page-charts__body">
            <!--<div-->
              <!--v-if="showTokensBig"-->
              <!--class="page-charts__aside"-->
            <!--&gt;-->
              <!--<the-tokens/>-->
            <!--</div>-->

            <div class="page-charts__main">
              <div class="page-charts__content">
                <div class="page-charts__graph">
                  <the-tradingview/>
                </div>

                <div class="page-charts__panels">
                  <div class="page-charts__col page-charts__trades">
                    <the-news-and-my-orders/>
                  </div>

                  <div class="page-charts__col page-charts__orders">
                    <the-trades/>
                  </div>
                </div>
              </div>

              <div class="page-charts__right">
                <the-panel-right :show-tokens="!showTokensBig"/>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>

    <the-order-popup v-if="orderConfirmationModal"/>

    <the-trade-details-popup v-if="tradeDetailsModal"/>

    <the-min-order-popup v-if="minOrderErrorModal"/>

    <the-delete-order-popup v-if="cancelOrderModal"/>

    <the-popup-error-order-balance v-if="errorOrderBalance"/>

    <the-popup-error-order-balance-reserved v-if="errorOrderBalanceReserved"/>

    <!-- popup and menu -->
    <the-popup v-if="isPopupShowing"/>
    <the-menu-sidebar v-if="isPopupShowing"/>
    <!-- popup and menu -->
  </div>
</template>

<script>
import debounce from 'lodash/debounce';
import { isValidEthereumChecksumAddress } from '@/util/crypto';
import { mapState, mapGetters } from 'vuex';
import { ASYNC_STATES, NOT_FOUND_PAGE } from '@/util/constants';

// Components
import TheHeaderInner from '@/containers/blocks/TheHeaderInner';
import TheTokens from '@/containers/blocks/TheTokens';
import TheTradingview from '@/containers/blocks/TheTradingview';
import TheNewsAndMyOrders from '@/containers/blocks/TheNewsAndMyOrders';
import TheTrades from '@/containers/blocks/TheTrades';
import ThePanelRight from '@/containers/blocks/ThePanelRight';
import TheOrderPopup from '@/containers/blocks/TheOrderPopup';
import TheTradeDetailsPopup from '@/containers/blocks/TheTradeDetailsPopup';
import TheMinOrderPopup from '@/containers/blocks/TheMinOrderPopup';
import TheDeleteOrderPopup from '@/containers/blocks/TheDeleteOrderPopup';
import ThePopupErrorOrderBalance from '@/containers/blocks/ThePopupErrorOrderBalance';
import ThePopupErrorOrderBalanceReserved from '@/containers/blocks/ThePopupErrorOrderBalanceReserved';
import ThePopup from '@/containers/blocks/ThePopup';
import TheMenuSidebar from '@/containers/blocks/TheMenuSidebar';

import { PAGE_TITLE } from '@/config';
import { chatMixin } from '@/util/mixins';

export default {
  name: 'page-charts',

  data() {
    return {
      showTokensBig: false,
      scrollSettings: {
        maxScrollbarLength: 348,
        minScrollbarLength: 121,
        wheelSpeed: 1,
        wheelPropagation: false,
        swipeEasing: false,
        suppressScrollY: true,
      },
    };
  },

  computed: {
    ...mapState({
      errorOrderBalance: state => state.ui.errorOrderBalance,
      errorOrderBalanceReserved: state => state.ui.errorOrderBalanceReserved,
    }),

    ...mapGetters({
      isPopupShowing: 'ui/isPopupShowing',
      orderConfirmationModal: 'activeOrder/orderConfirmation',
      tradeDetailsModal: 'trades/currentTradeDetails',
      minOrderErrorModal: 'activeOrder/minOrderError',
      cancelOrderModal: 'orders/cancelledOrder',
      activeTokenTitle: 'tokens/activeTokenTitle',
      windowSize: 'ui/windowSize',
    }),
  },

  watch: {
    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
    this.onResize();
  },

  mounted() {
    this.onResize();
    this.$ga.page(this.$router);
    this.$store.dispatch('tokens/listenToTokensChanges');
    // this.$store.dispatch('trades/listenToTradesChanges');
    // this.$store.dispatch('balance/listenToContractBalanceChanges');
    this.$store.dispatch('deposit/listenToBalanceOperations');
    this.$store.dispatch('tokens/loadTokens');
    this.$enableChat();
  },

  async beforeRouteUpdate(to, from, next) {
    const { token: paramToken, ...restParams } = to.params;
    const activeAddress = this.$store.getters['tokens/activeTokenContract'];
    const prevParam = from.params.token;

    const paramChanged = paramToken !== prevParam;
    const paramIsAddress = isValidEthereumChecksumAddress(paramToken);
    const paramIsActiveAddress = paramIsAddress && activeAddress === paramToken;
    const paramIsAddressOnExchange = paramIsAddress && this.$store.getters['tokens/isTokenContractOnExchange'](paramToken);

    if (paramToken === undefined) {
      next({
        name: 'tokens',
        // replace: true,
      });
    } else if (!paramChanged) {
      next();
    } else if (paramIsActiveAddress) {
      next({
        name: to.name,
        params: {
          ...restParams,
          token: prevParam,
        },
        // replace: true,
      });
    } else if (paramIsAddressOnExchange) {
      const nextToken = this.$store.getters['tokens/tokenByContractSimple'](paramToken);
      next({
        name: to.name,
        params: {
          ...restParams,
          token: `${nextToken.name}-${nextToken.shortName}`,
        },
        // replace: true,
      });
    } else if (paramIsAddress && !paramIsAddressOnExchange) {
      await this.$store.dispatch('tokens/setNewTokenAsActive', paramToken);
      if (this.$store.state.tokens.setActiveTokenState === ASYNC_STATES.ERROR) {
        next({
          name: NOT_FOUND_PAGE,
          // replace: true,
        });
      } else {
        next();
      }
    } else {
      const nextTokenAddress = this.$store.getters['tokens/getTokenContractByNameAndShortName'](paramToken);
      if (nextTokenAddress == null) {
        next({
          name: NOT_FOUND_PAGE,
          // replace: true,
        });
      } else if (nextTokenAddress !== activeAddress) {
        await this.$store.dispatch('tokens/setActiveToken', nextTokenAddress);
        next();
      } else {
        next();
      }
    }
  },

  beforeDestroy() {
    // this.$store.dispatch('trades/stopListeningToTradesChanges');
    this.$store.dispatch('tokens/stopListeningToTokensChanges');
    // this.$store.dispatch('balance/stopListeningToContractBalanceChanges');
    this.$store.dispatch('deposit/stopListeningToBalanceOperations');
  },

  methods: {
    onResize() {
      this.$nextTick(() => {
        this.showTokensBig = this.windowSize.width > 1921;
      });
    },
  },

  metaInfo() {
    return {
      title: `${this.activeTokenTitle}${PAGE_TITLE}`,
    };
  },

  components: {
    ThePopupErrorOrderBalanceReserved,
    ThePopupErrorOrderBalance,
    ThePopup,
    TheMenuSidebar,
    TheHeaderInner,
    TheTokens,
    TheTradingview,
    TheNewsAndMyOrders,
    TheTrades,
    ThePanelRight,
    TheDeleteOrderPopup,
    TheOrderPopup,
    TheTradeDetailsPopup,
    TheMinOrderPopup,
  },

  mixins: [chatMixin],
};
</script>


<style lang="scss">
@import '~@/scss/styles';

.page-charts {
  background-color: #f4f5f9;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  overflow-x: auto;

  &__wrapp {
    overflow: hidden;
    min-width: 1300px;
  }

  &__aside {
    flex-basis: 328px;
    max-width: 328px;
    padding: 0 9px 0 10px;
    box-sizing: border-box;

    @include rmin(1921) {
      padding: 0 8px 0 10px;
    }
  }

  &__body {
    display: flex;
    flex-wrap: wrap;
    min-width: 1024px;
    height: calc(100vh - 78px);

    @include r(1200) {
      height: calc(100vh - 58px);
    }

    @include h(750) {
      height: calc(100vh - 58px);
    }
  }

  &__content {
    background-color: #F8F9FC;
    box-shadow: 0 3px 3px rgba(black, .08);
    flex-basis: calc(100% - 397px);
    max-width: calc(100% - 397px);
    position: relative;
    height: 100%;
    z-index: 1;

    @include rmin(1921) {
      flex-basis: calc(100% - 494px);
      max-width: calc(100% - 494px);
    }
  }

  &__main {
    box-sizing: border-box;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    position: relative;
    height: 100%;

    @include r(1200) {
      flex-basis: 100%;
      max-width: 100%;
    }

    // @include rmin(1921) {
    //  flex-basis: calc(100% - 328px);
    //  max-width: calc(100% - 328px);
    // }
  }

  &__wrapp-content {
    box-sizing: border-box;
    position: relative;
    background-color: #f4f5f9;
    transform: translateZ(0);
  }

  &__panels {
    display: flex;
    flex-wrap: wrap;
    height: 45%;
    box-sizing: border-box;
    padding: calc(.2vh + 14px) 8px 12px 17px;

    @include r(1200) {
      padding-left: 11px;
      padding-right: 10px;
      padding-bottom: 8px;
    }

    @include h(750) {
      padding-left: 11px;
      padding-right: 10px;
      padding-bottom: 8px;
    }

    @include rwh(1921, 750) {
      padding: calc(.2vh + 14px) 29px 21px 27px;
    }
  }

  &__graph {
    height: 55%;
  }

  &__right {
    flex-basis: 397px;
    max-width: 397px;
    position: relative;
    height: 100%;
    overflow: hidden;
    display: flex;
    flex-flow: column;

    @include rmin(1921) {
      flex-basis: 494px;
      max-width: 494px;
    }
  }

  &__active-order {
    padding-bottom: 14px;
    border-bottom: 1px solid #e3e5f2;

    @include rmin(1921) {
      padding-bottom: 17px;
    }
  }

  &__order-book {
    min-height: calc(100vh - 81px - 418px);
  }

  &__col {
    box-sizing: border-box;

    &:first-child {
      padding-right: 7px;
    }

    &:last-child {
      padding-left: 5px;
    }

    @include r(1400) {
      &:first-child {
        padding-right: 5px;
      }

      &:last-child {
        padding-left: 5px;
      }
    }

    @include r(1200) {
      &:first-child {
        padding-right: 5px;
      }

      &:last-child {
        padding-left: 7px;
      }
    }

    @include rmin(1921) {
      &:first-child {
        padding-right: 19px;
      }

      &:last-child {
        padding-left: 16px;
      }
    }

    @include h(750) {
      &:first-child {
        padding-right: 5px;
      }

      &:last-child {
        padding-left: 7px;
      }
    }
  }

  &__trades {
    flex-basis: 50%;
    max-width: 50%;
    height: 100%;
  }

  &__orders {
    flex-basis: 50%;
    max-width: 50%;
    height: 100%;
  }
}
</style>



