<template>
  <div
    class="the-trades"
    :class="{ 'the-trades__loading': isPendingOrUnsync }"
  >
    <div class="the-trades__tabs">
      <e-tabs
        :value="activeTabId"
        :tabs="tabs"
        @input="handleTabChange"
      />
    </div>

    <!-- BEGIN:   /  -->
    <div class="the-trades__external-switch">
      <e-switch
        v-model="showOnlyInternalTrades"
        color="blue"
        size="small"
      >
        Hide external trades
      </e-switch>
    </div>
    <!-- END:   /  -->

    <!--         -->
    <e-preloader v-if="isPendingOrUnsync"/>

    <div
      v-else-if="trades.length !== 0"
      class="the-trades__body"
    >
      <!-- BEGIN:    -->
      <div class="the-trades__inner the-trades__inner_no-pagination">
        <e-container-scroll
          class="the-trades__wrapper"
          ref="scroll"
        >
          <div class="the-trades__list">
            <transition-group name="trades-list" :duration="1000">
              <e-transaction
                v-for="item in trades"
                :key="item.id"
                :buy-or-sell="item.type"
                :value="item.type === BUY_TOKEN_ORDER ? item.takerAmountFilled: item.makerAmountFilled"
                :eth-value="item.type === BUY_TOKEN_ORDER ?  item.makerAmountFilled : item.takerAmountFilled"
                :price="item.unitPrice"
                :time="item.createdAt"
                :exchange="item.exchange"
                :confirmation-state="mapOperationStatus(item.isConfirmed)"
                :decimals="activeToken.decimals"
                :is-my="isTabMyTradesActive"
                @click="showDetailsIfTradeIsMine(item)"
              />
            </transition-group>
          </div>
        </e-container-scroll>
      </div>
      <!-- END:    -->
    </div>

    <div
      class="the-trades__error"
      v-else
    >
      <e-panel-error
        :icon="errorIcon"
        buttons
      >
        <template slot="title">Your Trades</template>

        <template slot="descr">No active trades found {{ activeToken ? `with ${activeToken.name}` : '' }}</template>

        <div
          slot="after"
          class="the-trades__error-btns"
        >
          <div class="the-trades__error-btn the-trades__error-btn_sell">
            <div class="the-trades__error-btn-arrow">
              <e-icon :src="iconLoss"/>
            </div>
            <div class="the-trades__error-btn-text">
              sell {{ activeToken ? activeToken.shortName : '' }}
            </div>
          </div>
          <div class="the-trades__error-separator">or</div>
          <div class="the-trades__error-btn the-trades__error-btn_buy">
            <div class="the-trades__error-btn-text">
              buy {{ activeToken ? activeToken.shortName : '' }}
            </div>
            <div class="the-trades__error-btn-arrow">
              <e-icon :src="iconProfit"/>
            </div>
          </div>
        </div>
      </e-panel-error>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex';
import { mapOperationStatus } from '@/api/backend/helpers';
import { ASYNC_STATES } from '@/util/constants';
import { onReconnect, removeReconnectListener, getMyTrades, onTradeChange, removeTradeChangeHandler } from '@/api/backend';
import { onResume, removeResumeListener } from '@/util/IdleEmitter';
import { mapTrade } from '@/store/modules/trades/helpers';
import { PAGE_SIZE_TRADES } from '@/config';

// Components
import EContainerScroll from '@/components/EContainerScroll';
import EPanelError from '@/components/EPanelError';
import EPreloader from '@/components/EPreloader';
import ETabs from '@/components/ETabs';
import ETransaction from '@/components/ETransaction';
import ESwitch from '@/components/ESwitch';
import EIcon from '@/components/EIcon';

// Icons
import errorIcon from '@/icons/e-icon_empty-orders.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';

const TAB_ALL_TRADES = {
  id: 0,
  title: 'all trades',
};

const TAB_MY_TRADES = {
  id: 1,
  title: 'my trades',
};

const TABS_FOR_USER_WITH_WALLET = [
  TAB_ALL_TRADES,
  TAB_MY_TRADES,
];

const TABS_FOR_USER_WITHOUT_WALLET = [TAB_ALL_TRADES];

const getTradesWithTradeUpserted = ({ trade, trades }) => {
  const currentIndexOfNextElement = trades.findIndex(t => t.id <= trade.id);
  const result = [...trades];
  if (currentIndexOfNextElement !== -1 && trades[currentIndexOfNextElement].id === trade.id) {
    result[currentIndexOfNextElement] = trade;
    return result;
  }
  const indexToInsert = currentIndexOfNextElement !== -1 ? currentIndexOfNextElement : trades.length;
  result.splice(indexToInsert, 0, trade);
  return result.slice(0, PAGE_SIZE_TRADES);
};

/**
 * Precondition: getters['tokens/activeToken'] !== null.
 */
export default {
  name: 'the-trades',

  data() {
    return {
      activeTabId: TAB_ALL_TRADES.id,
      showOnlyInternalTrades: false,
      tradesRequestId: 0,
      myTrades: [],
      myTradesStatus: ASYNC_STATES.INITIAL,
      myTradesInSync: true,
      myTradesHandleNewTrade: null,
      myTradesHandleReconnect: null,
      myTradesHandleResume: null,
    };
  },

  computed: {
    ...mapGetters({
      userAddress: 'wallets/activeWalletAddress',
      activeToken: 'tokens/activeToken',
      latestTradesLoading: 'trades/latestTradesLoading',
    }),

    isTabMyTradesActive() {
      return this.activeTabId === TAB_MY_TRADES.id;
    },

    // TODO: handle all loading states

    isPendingOrUnsync() {
      const tabAllTradesIsPendingOrUnsync = this.activeTabId === TAB_ALL_TRADES.id && this.latestTradesLoading;
      const tabMyTradesPendingOrUnsync = (
        this.activeTabId === TAB_MY_TRADES.id &&
        (
          !this.myTradesInSync ||
          [ASYNC_STATES.INITIAL, ASYNC_STATES.PENDING].includes(this.myTradesStatus)
        )
      );
      return tabAllTradesIsPendingOrUnsync || tabMyTradesPendingOrUnsync;
    },

    tabs() {
      return this.$store.getters['wallets/isLogin'] ? TABS_FOR_USER_WITH_WALLET : TABS_FOR_USER_WITHOUT_WALLET;
    },

    trades() {
      return this.activeTabId === TAB_ALL_TRADES.id
        ? this.$store.state.trades.latestTrades
        : this.myTrades;
    },
  },

  watch: {
    userAddress() {
      if (this.activeTabId === TAB_ALL_TRADES.id) {
        // nothing
      } else {
        this.myTradesStatus = ASYNC_STATES.INITIAL;
        this.fetchMyTrades().then();
      }
    },

    activeToken() {
      if (this.activeTabId === TAB_ALL_TRADES.id) {
        this.$store.dispatch('trades/loadLatestTrades');
      } else {
        this.myTradesStatus = ASYNC_STATES.INITIAL;
        this.fetchMyTrades().then();
      }
    },

    showOnlyInternalTrades(showOnlyInternalTrades) {
      this.$store.commit('trades/SET_TRADES_MODE', showOnlyInternalTrades);
      if (this.activeTabId === TAB_ALL_TRADES.id) {
        this.$store.dispatch('trades/loadLatestTrades');
      } else {
        this.myTradesStatus = ASYNC_STATES.INITIAL;
        this.fetchMyTrades().then();
      }
    },
  },

  created() {
    Object.assign(this, {
      errorIcon,
      iconProfit,
      iconLoss,
    });
    this.fetchMyTrades = this.fetchMyTrades.bind(this);
    this.startListeningToAllTrades();
  },

  beforeDestroy() {
    if (this.activeTabId === TAB_ALL_TRADES.id) {
      this.stopListeningToAllTrades();
    } else {
      this.stopListeningToMyTrades();
    }
  },

  methods: {
    mapOperationStatus,

    handleTabChange(nextActiveTabId) {
      this.activeTabId = nextActiveTabId;
      if (this.activeTabId === TAB_ALL_TRADES.id) {
        this.stopListeningToMyTrades();
        this.startListeningToAllTrades();
      } else {
        this.stopListeningToAllTrades();
        this.startListeningToMyTrades();
      }
    },

    async fetchMyTrades() {
      const tradesRequestIdSent = this.tradesRequestId;
      const contractAddress = this.$store.getters['contracts/activeMainContractAddress'];
      const tokenAddress = this.$store.getters['tokens/activeTokenContract'];
      const tokenDecimals = this.$store.getters['tokens/activeToken'].decimals;
      const userAddress = this.$store.getters['wallets/activeWalletAddress'];
      const internalOnly = this.showOnlyInternalTrades;
      if (this.myTradesStatus === ASYNC_STATES.INITIAL) {
        this.myTradesStatus = ASYNC_STATES.PENDING;
      }
      try {
        const myTradesRaw = await getMyTrades({ contractAddress, tokenAddress, userAddress, internalOnly });
        const myTradesTransformed = myTradesRaw.map(t => mapTrade(t, userAddress, tokenDecimals));
        if (tradesRequestIdSent !== this.tradesRequestId) return; // fast exit if multiple simultaneous requests (like debounce)
        this.myTrades = myTradesTransformed;
        if (this.myTradesStatus === ASYNC_STATES.PENDING) {
          this.myTradesStatus = ASYNC_STATES.SUCCESS;
        }
      } catch (e) {
        if (this.myTradesStatus === ASYNC_STATES.PENDING) {
          this.myTradesStatus = ASYNC_STATES.ERROR;
        }
        throw e;
      }
    },

    /**
     * Part of the code in watchers
     */
    startListeningToMyTrades() {
      this.myTradesHandleNewTrade = (rawTrade) => {
        const tokenDecimals = this.$store.getters['tokens/activeToken'].decimals;
        const userAddress = this.$store.getters['wallets/activeWalletAddress'];
        const trade = mapTrade(rawTrade, userAddress, tokenDecimals);
        const tradePassesInternalOnlyFilter = !this.showOnlyInternalTrades || !trade.isExternal;
        const initializedAndNotSyncing = this.myTradesStatus === ASYNC_STATES.SUCCESS && this.myTradesInSync;
        if (
          trade.contractAddress === this.$store.getters['contracts/activeMainContractAddress'] &&
          trade.tokenAddress === this.$store.getters['tokens/activeTokenContract'] &&
          tradePassesInternalOnlyFilter &&
          trade.isMy &&
          initializedAndNotSyncing
        ) {
          // TODO: introduce buffer, while init or sync is pending
          this.myTrades = getTradesWithTradeUpserted({ trade, trades: this.myTrades });
        }
      };
      onTradeChange(this.myTradesHandleNewTrade);

      // add handlers
      const unsyncHandler = async () => {
        this.myTradesInSync = false;
        await this.fetchMyTrades()
          .finally(() => {
            this.myTradesInSync = true;
          })
          .then();
      };
      this.myTradesHandleReconnect = unsyncHandler;
      onReconnect(this.myTradesHandleReconnect);
      this.myTradesHandleResume = unsyncHandler;
      onResume(this.myTradesHandleResume);

      // init
      this.fetchMyTrades().then();
    },

    stopListeningToMyTrades() {
      // remove handlers
      removeTradeChangeHandler(this.myTradesHandleNewTrade);
      this.myTradesHandleNewTrade = null;
      removeReconnectListener(this.myTradesHandleReconnect);
      this.myTradesHandleReconnect = null;
      removeResumeListener(this.myTradesHandleResume);
      this.myTradesHandleResume = null;

      // reset state
      this.myTrades = [];
      this.myTradesStatus = ASYNC_STATES.INITIAL;
      this.myTradesInSync = true;
    },

    startListeningToAllTrades() {
      this.$store.dispatch('trades/listenToTradesChanges');
      this.$store.dispatch('trades/loadLatestTrades');
    },

    stopListeningToAllTrades() {
      this.$store.dispatch('trades/stopListeningToTradesChanges');
    },

    ...mapActions({
      showDetailsIfTradeIsMine: 'trades/showDetailsIfTradeIsMine',
    }),
  },

  components: {
    EContainerScroll,
    EPanelError,
    EPreloader,
    ETabs,
    ETransaction,
    ESwitch,
    EIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-trades {
  $this: &;
  position: relative;
  height: 100%;

  &__body {
    height: calc(100% - 27px);
    overflow: hidden;
    position: relative;
  }

  &__tabs {
    margin-bottom: 11px;
    padding-left: 13px;
  }

  &__error {
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    padding-bottom: 28px;
  }

  &__inner {
    height: calc(100% - 30px);
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }
  }

  &__inner_no-pagination {
    height: 100%;
  }

  &__pagination {
    margin-top: 10px;
  }

  &__wrapper {
    padding-left: 3px;
    padding-right: 10px;

    @include rmin(1921) {
      padding-right: 17px;
    }
  }

  &__external-switch {
    position: absolute;
    top: 0;
    right: 14px;
  }

  &__error-btns {
    display: flex;
    margin-top: 18px;
  }

  &__error-btn-arrow {
    height: 16px;
    width: 18px;
    display: inline-block;
    position: relative;
    top: 4px;
  }

  &__error-btn-text {
    color: $color-semi-blue;
    font-size: 13px;
    text-transform: uppercase;
    display: inline-block;
    font-weight: 700;
  }

  &__error-btn_sell {
    #{$this}__error-btn-arrow {
      margin-right: 7px;
    }
  }

  &__error-btn_buy {
    #{$this}__error-btn-arrow {
      margin-left: 7px;
    }
  }

  &__error-separator {
    font-size: 12px;
    color: $color-semi-blue;
    font-weight: 400;
    margin: 0 11px;
    padding-top: 1px;
  }
}

// 
.trades-list-move {
  transition: all 1s;
}

.the-trades__loading .trades-list-move {
  transition: none;
}

.trades-list-enter-active > .e-transaction__bg {
  transition: all 1s;
}

.trades-list-enter > .e-transaction__bg {
  opacity: 1;
  transition: all 1s;
}

.trades-list-enter-to > .e-transaction__bg {
  opacity: 0;
}
</style>



