<template>
  <transition-modal>
    <div class="the-trade-details-popup">
      <e-receipt
        :isConfirmed="trade.isConfirmed"
        :status="true"
        :btnLink="buttonLink"
        title="Trade details"
        @cancel="closeWindow"
      >
        <template slot="header">
          <div class="the-trade-details-popup__tabs">
            <!-- <e-button-tabs
              :tabs="buttonTabs"
              mod="default"
              @changeTab="handleChangeTab"
            /> -->
          </div>

          <!-- BEGIN:      (    ) -->
          <div class="the-trade-details-popup__container">
            <e-receipt-item
              :buy="trade.type === BUY_TOKEN_ORDER"
              :sell="trade.type === SELL_TOKEN_ORDER"
              :token-name="trade.type === BUY_TOKEN_ORDER ? `Buy ${trade.token.name}` : `Sell ${trade.token.name}`"
              :icon="true"
            >
              <template slot="title">
                Order Type
              </template>
              <template slot="value"></template>
            </e-receipt-item>

             <e-receipt-item :tokenName="trade.isTaker ? 'TAKER' : 'MAKER'">
              <template slot="title">
                Your order position
              </template>
              <template slot="value">

              </template>
            </e-receipt-item>

            <e-receipt-item tokenName="ETH">
              <template slot="title">
                Price for 1 {{ trade.token.shortName }}
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.unitPrice"
                  :decimal-places="6"
                />
              </template>
            </e-receipt-item>

            <e-receipt-item :tokenName="trade.token.shortName">
              <template slot="title">
                Amount {{ trade.token.shortName }}
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.tokenAmount"
                  :decimals="trade.token.decimals"
                  :decimal-places="6"/>
              </template>
            </e-receipt-item>

            <e-receipt-item  tokenName="ETH">
              <template slot="title">
                Transaction value
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.ethAmount"
                  :decimals="ETH_DECIMALS"
                  :decimal-places="6"/>
              </template>
            </e-receipt-item>
          </div>
          <!-- END:      (    ) -->
        </template>

        <template slot="body">
          <!-- BEGIN:     -->
          <div class="the-trade-details-popup__container" v-if="trade.isTaker">
            <e-receipt-item
              :tokenName="trade.taker.give.tokenShortName"
              :sell="true"
              :bold="true"
            >
              <template slot="title">
                Paid
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.taker.give.amount"
                  :decimals="trade.taker.give.decimals"
                  :decimal-places="6"
                />
              </template>
            </e-receipt-item>
            <e-receipt-item
              :tokenName="trade.taker.take.tokenShortName"
              :bold="true"
              v-if="trade.isTaker"
            >
              <template slot="title">
                Total Bought
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.maker.give.amount"
                  :decimals="trade.maker.give.decimals"
                  :decimal-places="6"
                />
              </template>
            </e-receipt-item>
          </div>
          <!-- END:     -->

          <!-- BEGIN:     -->
          <div class="the-trade-details-popup__container" v-else>
            <e-receipt-item
              :sell="true"
              :bold="true"
              :tokenName="trade.maker.give.tokenShortName"
            >
              <template slot="title">
                Paid
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.maker.give.amount"
                  :decimals="trade.maker.give.decimals"
                  :decimal-places="6"
                />
              </template>
            </e-receipt-item>
            <e-receipt-item
              :tokenName="trade.maker.take.tokenShortName"
              :bold="true"
              v-if="!trade.isTaker"
            >
              <template slot="title">
                Total Bought
              </template>
              <template slot="value">
                <e-big-number
                  :value="trade.taker.give.amount"
                  :decimals="trade.taker.give.decimals"
                  :decimal-places="6"
                />
              </template>
            </e-receipt-item>
          </div>
          <!-- END:     -->
        </template>

        <template slot="footer">
          <!-- BEGIN:     -->
          <div class="the-trade-details-popup__container" v-if="trade.isTaker">
            <e-receipt-item
              :tokenName="trade.taker.take.tokenShortName"
              :bold="true"
              :sell="true"
            >
              <template slot="title">
                Transaction Fee
              </template>
              <template slot="value">
                <e-big-number :value="trade.taker.take.fee" :decimals="trade.taker.take.decimals" :decimal-places="6"/>
              </template>
            </e-receipt-item>

            <e-receipt-item
              :tokenName="trade.taker.take.tokenShortName"
              :bold="true"
              :sell="true"
            >
              <template slot="title">
                Gas Fee
              </template>
              <template slot="value">
                <e-big-number :value="trade.taker.take.gasFee" :decimals="trade.taker.take.decimals" :decimal-places="6"/>
              </template>
            </e-receipt-item>

            <e-receipt-item
              :tokenName="trade.taker.take.tokenShortName"
              :bold="true"
              :buy="true"
            >
              <template slot="title">
                Received
              </template>
              <template slot="value">
                <e-big-number :value="trade.taker.take.amount" :decimals="trade.taker.take.decimals" :decimal-places="6"/>
              </template>
            </e-receipt-item>
          </div>
          <!-- END:     -->

          <!-- BEGIN:     -->
          <div class="the-trade-details-popup__container" v-else>
            <e-receipt-item
              :tokenName="trade.maker.take.tokenShortName"
              :bold="true"
              :sell="true"
            >
              <template slot="title">
                Transaction Fee
              </template>
              <template slot="value">
                <e-big-number :value="trade.maker.take.fee" :decimals="trade.maker.take.decimals" :decimal-places="6"/>
              </template>
            </e-receipt-item>

             <e-receipt-item
              :tokenName="trade.maker.take.tokenShortName"
              :bold="true"
              :buy="true"
            >
              <template slot="title">
                Received
              </template>
              <template slot="value">
                <e-big-number :value="trade.maker.take.amount" :decimals="trade.maker.take.decimals" :decimal-places="6"/>
              </template>
            </e-receipt-item>
          </div>
          <!-- END:     -->
        </template>
      </e-receipt>
    </div>
  </transition-modal>
</template>

<script>
import { mapMutations, mapGetters } from 'vuex';

import EBigNumber from '@/components/EBigNumber';
import EReceipt from '@/components/EReceipt';
import EReceiptItem from '@/components/EReceiptItem';
import EButtonTabs from '@/components/EButtonTabs';

import TransitionModal from '@/containers/transitions/TransitionModal';

import { ETHERSCAN_URL } from '@/config';

export default {
  name: 'the-trade-details-popup',

  data() {
    return {
      buttonTabs: ['taker', 'maker'],
      activeTabIndex: 0,
    };
  },

  computed: {
    ...mapGetters({
      trade: 'trades/currentTradeDetails',
    }),

    buttonLink() {
      const hash = this.trade.transactionHash;
      if (!hash) {
        return '';
      } else if (hash === '0x0') {
        return hash;
      }
      return `${ETHERSCAN_URL}/tx/${hash}`;
    },
  },

  methods: {
    ...mapMutations({
      close: 'trades/HIDE_TRADE_DETAILS',
    }),

    handleChangeTab(tabIndex) {
      this.activeTabIndex = tabIndex;
    },

    closeWindow() {
      this.activeTabIndex = 0;
      this.close();
    },
  },

  components: {
    EBigNumber,
    EReceipt,
    EReceiptItem,
    EButtonTabs,
    TransitionModal,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-trade-details-popup {
  &__tabs {
    margin-bottom: 20px;
  }
}
</style>



