<template>
  <div class="the-my-orders">
    <template v-if="isLogin && myOrdersPage.length !== 0 && !myOrdersPageLoading">
      <div
        class="the-my-orders__body"
        :class="{
          'the-my-orders__body_no-pagination': pageCount < 2,
          'is-loading': myOrdersPageLoading,
        }"
      >
        <e-container-scroll
          ref="scroll"
          class="the-my-orders__wrapper"
        >
          <transition-list>
            <e-order
              v-for="{
                orderHash,
                type,
                amount,
                amountFilled,
                canceledAt,
                createdAt,
                fulfilledAt,
                isActive,
                isCanceled,
                unitPrice,
                stopPrice,
                isStopped,
                isTradable,
                token,
              } in myOrdersPage"
              :key="orderHash"
              v-bind="{
                orderHash,
                amount,
                canceledAt,
                createdAt,
                fulfilledAt,
                isActive,
                isCanceled,
                unitPrice,
                stopPrice,
                isStopped,
              }"
              :orderType="type"
              :amountFilled="amountFilled.total"
              :amountApproved="amountFilled.approved"
              :amountPanding="amountFilled.pending"
              :decimals="token.decimals"
              :tokenShortName="token.shortName"
              :tokenImage="token.contract"
              :allowedDelete="isActiveMode && isTradable"
            />
          </transition-list>
        </e-container-scroll>

        <e-order-attention-hint
          v-if="orderAttentionOffset > 0"
          v-click-outside="() => { orderAttentionOffset = -1 }"
          :style="{ top: `${orderAttentionOffset - 12}px` }"
        />
      </div>

      <!-- BEGIN: Pagination -->
      <e-pagination
        v-if="pageCount > 1"
        class="the-trades__pagination"
        v-model="activePageNumber"
        :page-count="pageCount"
        type="small"
      />
      <!-- END: Pagination -->
    </template>

    <div
      v-else-if="!isLogin"
      class="the-my-orders__error the-my-orders__error_margin"
    >
      <e-panel-error :icon="errorIcon">
        <template slot="title">
          Your Orders
        </template>
        <template slot="descr">Here you'll find your orders. But you need to be logged in.</template>
      </e-panel-error>
    </div>


    <div
      v-else-if="isLogin"
      class="the-my-orders__error"
    >
      <e-panel-error :icon="errorIcon">
        <template slot="title">
          Your Orders
        </template>
        <template slot="descr">Here you'll find your orders.</template>
      </e-panel-error>
    </div>

    <!--         -->
    <div
      v-if="myOrdersPageLoading"
      class="the-my-orders__preloader"
    >
      <e-preloader/>
    </div>
  </div>
</template>

<script>
/**
 *     
 *
 * @version 1.0.4
 */

import { mapGetters, mapMutations, mapActions } from 'vuex';
import { MY_ORDERS_MODE_ACTIVE } from '@/store/constants';

// omponents
import EContainerScroll from '@/components/EContainerScroll';
import EOrder from '@/components/EOrder';
import EOrderAttentionHint from '@/components/EOrderAttentionHint';
import EPagination from '@/components/EPagination';
import EPanelError from '@/components/EPanelError';
import EPreloader from '@/components/EPreloader';
import ETransaction from '@/components/ETransaction';

// Transitions
import TransitionList from '@/containers/transitions/TransitionList';

// Icons
import errorIcon from '@/icons/e-icon_empty-trades.svg';

export default {
  name: 'the-my-orders',

  data() {
    return {
      orderAttentionOffset: -1,
      errorIcon,

      //    
      activePageNumber: 0,
    };
  },

  computed: {
    isActiveMode() {
      return this.mode === MY_ORDERS_MODE_ACTIVE;
    },

    //    
    pageCount() {
      return Math.ceil(this.totalNumberOfOrders / this.ordersPerPage);
    },

    myOrdersPage() {
      return this.$store.getters['orders/myOrdersPage'];
    },

    myOrdersPageLoading() {
      return this.$store.getters['orders/myOrdersPageLoading'] || !this.$store.getters['blockchain/gasPriceLoaded'];
    },

    ...mapGetters({
      isLogin: 'wallets/isLogin',
      activeWallet: 'wallets/activeWallet',
      mode: 'orders/myOrdersMode',
      ordersPerPage: 'orders/ordersPerPage',
      totalNumberOfOrders: 'orders/totalNumberOfOrders',
    }),
  },

  watch: {
    mode() {
      this.setPage(0);
      this.loadMyOrdersPage();
    },

    activePageNumber(value) {
      this.setPage(value);
      this.loadMyOrdersPage();

      if (this.$refs.scroll && this.$refs.scroll.$el) {
        this.$refs.scroll.$el.scrollTop = 0;
      }
    },

    activeWallet: 'initMyOrders',
  },

  created() {
    this.loadMyOrdersPage();
    this.$store.dispatch('orders/listenToOrdersChanges');
    this.$store.dispatch('blockchain/listenToGasPriceChanges');
  },

  beforeDestroy() {
    this.$store.dispatch('orders/stopListeningToOrdersChanges');
    this.$store.dispatch('blockchain/stopListeningToGasPriceChanges');
  },

  methods: {
    ...mapMutations({
      setPage: 'orders/SET_MY_ORDERS_PAGE_NUMBER',
    }),

    ...mapActions({
      initMyOrders: 'orders/initMyOrders',
      loadMyOrdersPage: 'orders/loadMyOrdersPage',
    }),
  },

  components: {
    EContainerScroll,
    EOrder,
    EOrderAttentionHint,
    EPagination,
    EPanelError,
    EPreloader,
    ETransaction,
    TransitionList,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-my-orders {
  position: relative;
  height: 100%;

  &__tabs {
    padding-left: 11px;
    margin-bottom: 11px;
  }

  &__wrapper {
    padding-left: 3px;
    padding-bottom: 3px;
    padding-right: 12px;

    @include rmin(1921) {
      padding-right: 20px;
    }
  }

  &__error {
    height: 100%;
    width: 100%;
    padding-bottom: 28px;
    padding-right: 10px;
    box-sizing: border-box;
  }

  &__error_margin {
    margin-top: 27px;

    @include rmin(1921) {
      margin-top: 32px;
    }
  }

  &__body {
    position: relative;
    height: calc(100% - 57px);
    transition: opacity 0.5s ease-in-out;

    &.is-loading {
      opacity: 0.2;
    }
  }

  &__body_no-pagination {
    height: calc(100% - 27px);
  }

  &__preloader {
    position: absolute;
    top: 0;
    left: 0;
    right: 10px;
    bottom: 0;
  }
}
</style>



