<template>
  <div class='the-trading-view'>
    <div id='tv_chart_container' class='the-trading-view__chart'></div>
  </div>
</template>

<script>
import { BACKEND_URL } from '@/config';
import { mapGetters } from 'vuex';

const timezonesOffsets = {
  '300:240': 'America/New_York',
  '480:420': 'America/Los_Angeles',
  '420:420': 'America/Phoenix',
  '360:300': 'America/Chicago',
  '180:180': 'America/Argentina/Buenos_Aires',
  '360:360': 'America/El_Salvador',
  '180:120': 'America/Sao_Paulo',
  '300:300': 'America/Bogota',
  '240:240': 'America/Caracas',
  '600:600': 'Pacific/Honolulu',
  '420:360': 'US/Mountain',
  '-180:-240': 'Europe/Moscow',
  '-120:-180': 'Europe/Athens',
  '-60:-120': 'Europe/Berlin',
  '0:-60': 'Europe/London',
  '-660:-600': 'Australia/Sydney',
  '-600:-600': 'Australia/Brisbane',
  '-630:-570': 'Australia/Adelaide',
  '-360:-360': 'Asia/Almaty',
  '-300:-300': 'Asia/Ashkhabad',
  '-540:-540': 'Asia/Tokyo',
  '-480:-480': 'Asia/Taipei',
  '-210:-270': 'Asia/Tehran',
  '-240:-240': 'Asia/Dubai',
  '-330:-330': 'Asia/Kolkata',
  '-420:-420': 'Asia/Bangkok',
  '-780:-720': 'Pacific/Auckland',
  '-765:-825': 'Pacific/Chatham',
  '-780:-780': 'Pacific/Fakaofo',
  '-120:-120': 'Africa/Johannesburg',
  '-345:-345': 'Asia/Kathmandu',
};

export default {
  name: 'the-tradingview',

  data() {
    return {
      //  Bars = 0, Candles = 1, Line = 2, Area = 3, Heiken Ashi = 8, Hollow Candles = 9, Renko = 4, Kagi = 5
      // Point&Figure = 6, Line Break = 7, Baseline = 10
      graphicType: 1,
      activeTokenContractData: '',
    };
  },

  computed: {
    ...mapGetters({
      activeTokenContract: 'tokens/activeTokenContract',
      activeToken: 'tokens/activeToken',
    }),
  },

  watch: {
    activeToken() {
      if (this.activeTokenContractData !== this.activeToken.contract) {
        this.tradingViewInit();
        this.activeTokenContractData = this.activeToken.contract;
      }
    },
  },

  created() {},

  beforeDestroy() {
    this.tvWidget = undefined;
  },

  mounted() {
    this.tradingViewInit();
    this.activeTokenContractData = this.activeToken.contract;
  },

  methods: {
    getTimezone() {
      const winterOffset = new Date('2010-01-01T00:00:00').getTimezoneOffset();
      const summerOffset = new Date('2010-06-01T00:00:00').getTimezoneOffset();
      return timezonesOffsets[`${winterOffset}:${summerOffset}`];
    },

    tradingViewInit() {
      const activeToken = this.activeToken;

      if (this.tvWidget && this.tvWidget.chart()) {
        this.graphicType = this.tvWidget.chart().chartType();
      }

      const graphicType = this.graphicType;

      const defaultProps = {
        symbol: `${activeToken.shortName}/ETH`,
        interval: '60',
        containerId: 'tv_chart_container',
        libraryPath: '/static/charting_library/',
        userId: 'public_user_id',
        fullscreen: false,
        autosize: true,
      };

      const widgetOptions = {
        symbol: `${activeToken.contract}_0x0_${activeToken.decimals}_18`,
        datafeed: new window.Datafeeds.UDFCompatibleDatafeed(`${BACKEND_URL}/api/trades/candles`),
        debug: false,
        editorFontsList: ['Arial', 'sans-serif'],
        interval: defaultProps.interval,
        container_id: defaultProps.containerId,
        library_path: defaultProps.libraryPath,
        timeframe: '5D',
        timezone: this.getTimezone(),
        locale: 'en',
        disabled_features: [
          'use_localstorage_for_settings',
          'header_symbol_search',
          'header_undo_redo',
          'header_screenshot',
          'header_compare',
          'header_saveload',
          'header_settings',
          'study_buttons_in_legend',
          'format_button_in_legend',
          'show_hide_button_in_legend',
          'delete_button_in_legend',
          'control_bar',
        ],
        enabled_features: [
          'create_volume_indicator_by_default',
          'volume_force_overlay',
        ],
        user_id: defaultProps.userId,
        fullscreen: defaultProps.fullscreen,
        autosize: defaultProps.autosize,
        overrides: {
          'paneProperties.background': '#f8f9fc',
          'mainSeriesProperties.style': graphicType,
          'paneProperties.crossHairProperties.color': '#989898',
          'mainSeriesProperties.candleStyle.upColor': '#6FB36F',
          'mainSeriesProperties.candleStyle.borderUpColor': '#278947',
          'mainSeriesProperties.candleStyle.downColor': '#EB6265',
          'mainSeriesProperties.candleStyle.borderDownColor': '#CA4144',
          'scalesProperties.lineColor': '#E9ECF2',
          'mainSeriesProperties.areaStyle.color1': '#92AEC8',
          'mainSeriesProperties.areaStyle.color2': '#f8f9fc',
          'mainSeriesProperties.areaStyle.linewidth': 2,
          'mainSeriesProperties.areaStyle.linecolor': '#4A6E8E',
          'mainSeriesProperties.lineStyle.color': '#4A6E8E',
          'mainSeriesProperties.lineStyle.linewidth': 2,
          'mainSeriesProperties.baselineStyle.topLineWidth': 2,
          'mainSeriesProperties.baselineStyle.bottomLineWidth': 2,
          'mainSeriesProperties.baselineStyle.topFillColor2': 'rgba(111, 179, 111, .1)',
          'mainSeriesProperties.baselineStyle.bottomFillColor1': 'rgba(111, 179, 111, .1)',
          'mainSeriesProperties.baselineStyle.bottomFillColor2': '#CA4144',
          'mainSeriesProperties.baselineStyle.topLineColor': '#278947',
          'mainSeriesProperties.baselineStyle.bottomLineColor': '#CA4144',
          // 'mainSeriesProperties.candleStyle.barColorsOnPrevClose': false,
        },
      };

      // eslint-disable-next-line new-cap,no-unused-vars
      this.tvWidget = new window.TradingView.widget(widgetOptions);
    },
  },

  components: {},
};
</script>

<style lang='scss'>
@import '~@/scss/styles';

.the-trading-view {
  position: relative;
  height: 100%;

  &__chart {
    width: 100%;
    height: 100%;
    position: relative;
    z-index: 21;
    padding-top: 12px;
  }
}
</style>




