<template>
  <div class="the-panel-right">
    <div class="the-panel-right__top">
      <the-active-token :tokens-mode-selected.sync="tokensModeSelected"/>
    </div>

    <transition-dropdown>
      <div
        v-if="showTokens"
        v-show="tokensModeSelected"
        class="the-panel-right__tokens transition-dropdown-absolute"
      >
        <the-tokens  v-if="showTokens && tokensModeSelected" @active-token-change="tokensModeSelected = false"/>
      </div>
    </transition-dropdown>

    <transition-dropdown>
      <div
        v-show="!tokensModeSelected"
        class="the-panel-right__body transition-dropdown-absolute transition-dropdown-up"
      >
        <transition-list class="the-panel-right__body-list">
          <the-order
            v-show="!orderBookToggler"
            :key="1"
            class="the-panel-right__order"
          />

          <div
            :key="2"
            class="the-panel-right__order-book"
          >
            <the-order-book
              :orderBookState="orderBookToggler"
              @orderBookToggle="toggleOrderBookMode"
            />
          </div>
        </transition-list>
      </div>
    </transition-dropdown>
  </div>
</template>

<script>
import debounce from 'lodash/debounce';
import { mapGetters } from 'vuex';

import TheOrder from '@/containers/blocks/TheOrder';
import TheOrderBook from '@/containers/blocks/TheOrderBook';
import TheActiveToken from '@/containers/blocks/TheActiveToken';
import TheTokens from '@/containers/blocks/TheTokens';

import TransitionDropdown from '@/containers/transitions/TransitionDropdown';
import TransitionList from '@/containers/transitions/TransitionList';

export default {
  name: 'the-panel-right',


  props: {
    showTokens: Boolean,
  },

  data() {
    return {
      tokensModeSelected: false,
      orderBookToggler: false,
    };
  },

  computed: {
    ...mapGetters({
      windowSize: 'ui/windowSize',
    }),
  },

  watch: {
    windowSize: {
      deep: true,
      handler() {
        this.onResize();
      },
    },
  },

  created() {
    this.onResize = debounce(this.onResize.bind(this), 200);
  },

  mounted() {
    this.$nextTick(this.onResize);
  },

  methods: {
    toggleOrderBookMode() {
      this.orderBookToggler = !this.orderBookToggler;
    },
    onResize() {
      this.$nextTick(() => {
        if (this.tokensModeSelected && this.windowSize.width > 1921) {
          this.tokensModeSelected = false;
        }
      });
    },
  },

  components: {
    TheOrder,
    TheOrderBook,
    TheActiveToken,
    TheTokens,
    TransitionDropdown,
    TransitionList,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-panel-right {
  position: relative;
  height: 100%;

  &__body {
    position: relative;
    height: calc(100% - 70px);

    @include rhw(750, 1921) {
      height: calc(100% - 60px);
    }

    @include r(1200) {
      height: calc(100% - 60px);
    }
  }

  &__body-list {
    display: flex;
    flex-flow: column;
    height: 100%;
  }

  &__order {
    z-index: 1;
  }

  &__order-book {
    flex: 1;
    overflow: hidden;
    background-color: #f4f5f9;
  }

  &__tokens {
    padding: 0 18px 0 16px;
    position: relative;
    height: calc(100% - 88px);

    @include rhw(750, 1921) {
      height: calc(100% - 60px);
      padding-bottom: 20px;
    }

    @include r(1200) {
      height: calc(100% - 60px);
      padding-right: 8px;
      padding-bottom: 20px;
    }
  }
}
</style>



