<template>
  <div class="the-news-and-my-orders">
    <div
      class="the-news-and-my-orders__tabs"
    >
      <e-tabs
        :tabs="tabs"
        v-model="activeTab"
      />
    </div>

    <the-news v-if="activeTab === NEWS_AND_ORDERS_TAB_NEWS"/>
    <the-my-orders v-else-if="[NEWS_AND_ORDERS_TAB_ACTIVE, NEWS_AND_ORDERS_TAB_HISTORY].includes(activeTab)"/>
  </div>
</template>

<script>
/**
 *       
 *
 * @version 1.0.4
 */

import { NEWS_AND_ORDERS_TAB_ACTIVE, NEWS_AND_ORDERS_TAB_HISTORY, MY_ORDERS_MODE_ACTIVE, MY_ORDERS_MODE_HISTORY } from '@/store/constants';

// omponents
import TheNews from '@/containers/blocks/TheNews';
import TheMyOrders from '@/containers/blocks/TheMyOrders';
import ETabs from '@/components/ETabs';

const TAB_NEWS = {
  id: 0,
  title: 'News',
};

const TAB_ACTIVE_ORDERS = {
  id: 1,
  title: 'Active orders',
};

const TAB_HISTORY = {
  id: 2,
  title: 'Order history',
};

const TABS_FOR_USER_WITH_WALLET = [
  TAB_NEWS,
  TAB_ACTIVE_ORDERS,
  TAB_HISTORY,
];

const TABS_FOR_USER_WITHOUT_WALLET = [TAB_NEWS];

export default {
  name: 'the-news-and-my-orders',

  computed: {
    /**
     *     
     */
    activeTab: {
      get() {
        return this.$store.state.ui.newsAndMyOrdersTab;
      },

      set(activeTab) {
        this.$store.commit('ui/SET_NEWS_AND_ORDERS_TAB', activeTab);
        // TODO: infer my orders mode from newsAndOrders mode
        if (activeTab === NEWS_AND_ORDERS_TAB_ACTIVE) {
          this.$store.commit('orders/SET_MY_ORDERS_MODE', MY_ORDERS_MODE_ACTIVE);
        } else if (activeTab === NEWS_AND_ORDERS_TAB_HISTORY) {
          this.$store.commit('orders/SET_MY_ORDERS_MODE', MY_ORDERS_MODE_HISTORY);
        }
      },
    },

    /**
     *   
     *    ,        ,    
     */
    tabs() {
      return this.$store.getters['wallets/isLogin'] ? TABS_FOR_USER_WITH_WALLET : TABS_FOR_USER_WITHOUT_WALLET;
    },
  },

  components: {
    TheNews,
    TheMyOrders,
    ETabs,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-news-and-my-orders {
  position: relative;
  height: 100%;

  &__tabs {
    padding-left: 24px;
    margin-bottom: 11px;
  }
}
</style>



