<template>
  <div class="the-token-no-result">
    <div class="the-token-no-result__icon"><e-icon :src="iconBadResult"/></div>
    <div class="the-token-no-result__title">Oops</div>
    <div class="the-token-no-result__descr">
      You have no favorite tokens yet. Browse our token catalogue and add to your favorites by clicking on the star next to the interested token.
    </div>
    <div
      v-if="metamaskWalletActive"
      class="the-token-no-result__btn"
    >
      <e-button
        fill="green"
        :icon="iconPlus"
        width="space"
        size="m"
        padding="spacing-mod"
        @click="displayListYourToken"
      >
        List Your Token
      </e-button>
    </div>
    <div class="the-token-no-result__btn the-token-no-result__btn_sm">
      <e-button
        fill="white"
        :icon="iconRefresh"
        padding="smaller"
        width="space"
        size="s"
        iconMargin="primary"
        type="tiny"
        @click="clearSearchLine"
      >
        reset filter
      </e-button>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex';

import iconBadResult from '@/icons/e-icon_bad-result.svg';
import iconPlus from '@/icons/e-icon_plus-circle.svg';
import iconRefresh from '@/icons/e-icon_refresh_green.svg';

import EIcon from '@/components/EIcon';
import EButton from '@/components/EButton';

import { POPUP_LIST_TOKEN } from '@/store/constants';
import { METAMASK_WALLET_TYPE } from '@/util/constants';
import { routesMixin } from '@/util/mixins';

export default {
  name: 'the-token-no-result',

  data() {
    return {
      iconBadResult,
      iconPlus,
      iconRefresh,
    };
  },

  computed: {
    ...mapGetters({
      activeWallet: 'wallets/activeWallet',
    }),

    metamaskWalletActive() {
      return this.activeWallet ? this.activeWallet.type === METAMASK_WALLET_TYPE : false;
    },
  },

  methods: {
    displayListYourToken() {
      this.$router.push(this.$popupRoute(POPUP_LIST_TOKEN));
    },
    clearSearchLine() {
      this.$emit('clearSearchLine');
    },
  },

  components: {
    EIcon,
    EButton,
  },

  mixins: [routesMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-token-no-result {
  position: relative;
  border-radius: 3px;
  background-color: #ffffff;
  min-height: 228px;
  box-sizing: border-box;
  padding: 57px 20px 62px;

  &__title {
    color: $color-blue;
    font-size: 20px;
    font-weight: 600;
    margin-bottom: 15px;
    margin-top: 23px;
    padding-bottom: 15px;
    position: relative;
    text-align: center;

    &:before {
      background-color: $color-blue;
      bottom: 0;
      content: '';
      height: 2px;
      position: absolute;
      left: 50%;
      transform: translateX(-50%);
      width: 92px;
    }
  }

  &__descr {
    font-weight: 400;
    font-size: 14px;
    color: $color-ultramarine;
    line-height: 1.5em;
    margin: 0 auto 26px;
    max-width: 222px;
    text-align: center;
  }

  &__icon {
    height: 117px;
    margin: 0 auto;
    width: 154px;
    position: relative;
    left: -6px;
  }

  &__btn {
    margin: 0 auto;
    width: 215px;

    &:not(:last-of-type) {
      margin-bottom: 20px;
    }
  }

  &__btn_sm {
    width: 140px;
  }

}
</style>



