<template>
  <transition-modal>
    <div
      v-if="order"
      class="the-order-confirm"
    >
      <e-receipt
        title="Confirm order"
        @confirm="submit"
        @cancel="close"
      >
        <template slot="header">
          <e-receipt-item
            :buy="order.buyOrSell === BUY_TOKEN_ORDER"
            :sell="order.buyOrSell === SELL_TOKEN_ORDER"
            :token-name="displayedOrderType"
            icon
          >
            <template slot="title">
              Order Type
            </template>
          </e-receipt-item>

          <e-receipt-item
            v-if="order.type === ORDER_TYPE_LIMIT"
            tokenName="ETH"
          >
            <template slot="title">
              Price for 1 {{ activeToken.shortName }}
            </template>
            <e-big-number
              slot="value"
              :value="order.limitPrice"
              :decimals="ETH_DECIMALS"
              :decimal-places="6"
            />
          </e-receipt-item>

          <e-receipt-item
            v-if="order.type === ORDER_TYPE_STOP_LIMIT"
            tokenName="ETH"
          >
            <template slot="title">
              Limit Price for 1 {{ activeToken.shortName }}
            </template>
            <e-big-number
              slot="value"
              :value="order.limitPrice"
              :decimals="ETH_DECIMALS"
              :decimal-places="6"
            />
          </e-receipt-item>

          <e-receipt-item v-else-if="order.type === ORDER_TYPE_MARKET">
            <template slot="title">
              Price for 1 {{ activeToken.shortName }}
            </template>
            <div
              slot="value"
              class="the-order-confirm__value"
            >
              Market Price
            </div>
          </e-receipt-item>

          <e-receipt-item
            v-if="order.type === ORDER_TYPE_STOP_LIMIT"
            tokenName="ETH"
          >
            <template slot="title">
              Stop Price for 1 {{ activeToken.shortName }}
            </template>
            <e-big-number
              slot="value"
              :value="order.stopPrice"
              :decimals="ETH_DECIMALS"
              :decimal-places="6"
            />
          </e-receipt-item>

          <e-receipt-item :tokenName="activeToken.shortName">
            <template slot="title">
              Amount {{ activeToken.shortName }}
            </template>
            <template slot="value">
              <e-big-number :value="order.amount" :decimals="activeToken.decimals" :decimal-places="6"/>
            </template>
          </e-receipt-item>

          <e-receipt-item
            v-if="[ORDER_TYPE_LIMIT, ORDER_TYPE_STOP_LIMIT].includes(order.type)"
            tokenName="ETH"
          >
            <template slot="title">
              Transaction value
            </template>
            <template slot="value">
              <e-big-number :value="order.total" :decimals="ETH_DECIMALS" :decimal-places="6"/>
            </template>
          </e-receipt-item>

          <e-receipt-item
            v-else-if="order.type === ORDER_TYPE_MARKET"
          >
            <template slot="title">
              Transaction value
            </template>
            <div
              slot="value"
              class="the-order-confirm__value"
            >
              
            </div>
          </e-receipt-item>
        </template>
      </e-receipt>
    </div>
  </transition-modal>
</template>

<script>
import {
  BUY_TOKEN_ORDER,
  ORDER_TYPE_MARKET,
  ORDER_TYPE_STOP_LIMIT,
  SELL_TOKEN_ORDER,
} from '@/util/constants';
import { mapGetters, mapMutations, mapActions } from 'vuex';

import iconLink from '@/icons/e-icon_link.svg';

import EButton from '@/components/EButton';
import EIcon from '@/components/EIcon';
import EModal from '@/components/EModal';
import EBigNumber from '@/components/EBigNumber';
import EReceipt from '@/components/EReceipt';
import EReceiptItem from '@/components/EReceiptItem';

import TransitionModal from '@/containers/transitions/TransitionModal';

export default {
  name: 'the-order-popup',

  computed: {
    ...mapGetters({
      activeToken: 'tokens/activeToken',
      order: 'activeOrder/orderConfirmation',
    }),

    displayedOrderType() {
      const tokenSymbol = this.activeToken.shortName;
      const prefix = {
        [ORDER_TYPE_MARKET]: 'Market',
        [ORDER_TYPE_STOP_LIMIT]: 'Stop',
      }[this.order.type] || '';
      const buyOrSell = {
        [BUY_TOKEN_ORDER]: 'Buy',
        [SELL_TOKEN_ORDER]: 'Sell',
      }[this.order.buyOrSell];
      return `${prefix} ${buyOrSell} ${tokenSymbol}`;
    },
  },

  created() {
    Object.assign(this, { iconLink });
  },

  methods: {
    ...mapMutations({ close: 'activeOrder/ORDER_REJECT_CONFIRMATION' }),
    ...mapActions({ submit: 'activeOrder/submitOrder' }),
  },

  components: {
    TransitionModal,
    EBigNumber,
    EButton,
    EIcon,
    EModal,
    EReceipt,
    EReceiptItem,
  },
};
</script>

<style lang="scss">
.the-order-confirm__value {
  color: #0D105B;
  font-weight: 600;
  letter-spacing: 0.7px;
  text-transform: uppercase;
}
</style>



