<template>
  <div
    class="e-order"
    :class="[
      orderType === BUY_TOKEN_ORDER ? 'e-order_buy' : 'e-order_sell',
      {
        'e-order_canceled': isCanceled,
        'e-order_filled': isFilled,
      }
    ]"
  >
    <!-- TODO: Ruslan         e-order__attention -->
    <div class="e-order__top">
      <div class="e-order__total-token">
        <span class="e-order__icon-status">
          <e-icon :src="orderType === BUY_TOKEN_ORDER ? iconProfit : iconLoss"/>
        </span>

        <div class="e-order__amount-tokens">
          {{orderType === BUY_TOKEN_ORDER ? 'BUY' : 'SELL'}}
          <e-big-number :value="amount" :decimals="decimals" :decimal-places="3"/>
          {{tokenShortName}} @
        </div>

        <div class="e-order__price">
          <e-big-number :value="unitPrice" :decimal-places="9"/>
          <span class="e-order__price-icon">
            <e-icon :src="iconEarned"/>
          </span>
        </div>

        <!-- BEGIN:    -->
        <div class="e-order__price" v-if="stopPrice">
          with STOP @
          <e-big-number :value="stopPrice" :decimal-places="9"/>
          <span class="e-order__price-icon">
            <e-icon :src="iconEarned"/>
          </span>
        </div>
        <!-- END:    -->

        <!-- BEGIN:    -->
        <div
          v-else
          class="e-order__price e-order__price_total"
        >
          
          <e-big-number :value="ethAmount" :decimals="ETH_DECIMALS" :decimal-places="9" />
          <span class="e-order__price-icon">
            <e-icon :src="iconEarned"/>
          </span>
        </div>
        <!-- END:    -->
      </div>

      <div
        v-if="allowedDelete"
        class="e-order__delete"
        :class="[isTooSmall && isActive ? 'e-order__delete_small-order' : '']"
      >
        <e-icon
          @click="handleCancel({ orderHash, orderType })"
          :src="iconTrash"
        />
      </div>
      <div
        v-else
        class="e-order__condition"
      >
        <div class="e-order__condition-hint">
          <template v-if="isCanceled">
            canceled order @ {{canceledDateString}}
          </template>
          <template v-else>
            finished order @ {{filledDateString}}
          </template>
        </div>
      </div>

      <!-- BEGIN:  ,     -->
      <div
        v-if="isTooSmall && isActive"
        class="e-order__attention e-order__attention_to-small"
      >
        <e-icon
          :src="iconFlag"
        />
      </div>
      <!-- END:  ,     -->
    </div>

    <div class="e-order__body">
      <div class="e-order__token-image">
        <e-token-icon :contract="tokenImage"/>
      </div>
      <div class="e-order__wrapper">
        <div class="e-order__filled">
          <e-big-number :value="amountFilled" :decimals="decimals" :decimal-places="3"/>
          <span class="e-order__shortname">{{tokenShortName}}</span>
          <div class="e-order__hint">filled</div>
        </div>
        <div class="e-order__left">
          <e-big-number :value="amountLeft" :decimals="decimals" :decimal-places="3"/>
          <span class="e-order__shortname">{{tokenShortName}}</span>
          <div class="e-order__hint">left</div>
        </div>
        <div class="e-order__timer">
          {{createdDateString}}
        </div>
      </div>
      <div class="e-order__rail">
        <div class="e-order__rail-fill" :style="{width : barApproved + '%' }"></div>
        <div class="e-order__rail-fill e-order__rail-fill_filled" :style="{width : barPanding + '%' }"></div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex';
import { BigNumber } from 'bignumber.js';
import { dateToString } from '@/util/dates';
import { ETH_DECIMALS } from '@/util/constants';

// SVG icons
import iconEarned from '@/icons/e-icon_earned.svg';
import iconFlag from '@/icons/e-icon_flag.svg';
import iconLoss from '@/icons/e-icon_loss.svg';
import iconProfit from '@/icons/e-icon_profit.svg';
import iconTrash from '@/icons/e-icon_trash.svg';

// Components
import EIcon from '@/components/EIcon';
import EBigNumber from '@/components/EBigNumber';
import ETokenIcon from '@/components/ETokenIcon';
import EOrderAttentionHint from '@/components/EOrderAttentionHint';

export default {
  name: 'e-order',

  props: {
    //   (  )
    orderType: {
      type: String,
    },

    orderHash: {
      type: String,
      required: true,
    },

    //    
    amount: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // ,     ( )
    amountFilled: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //  ,     ( )
    amountApproved: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // ,       ( )
    amountPanding: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //   
    createdAt: {
      type: Date,
      default: null,
    },

    //   
    canceledAt: {
      type: Date,
      default: null,
    },

    //      
    fulfilledAt: {
      type: Date,
      default: null,
    },

    isActive: {
      type: Boolean,
      default: false,
    },

    isStopped: {
      type: Boolean,
      default: false,
    },

    //    
    isCanceled: {
      type: Boolean,
      default: false,
    },

    //  
    unitPrice: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    //  
    stopPrice: {
      type: BigNumber,
      default: () => null,
    },

    //       
    decimals: {
      type: Number,
      default: 18,
    },

    //    
    tokenShortName: {
      type: String,
      default: '',
    },


    // URL  
    tokenImage: {
      type: String,
      default: '',
    },

    //     
    allowedDelete: {
      type: Boolean,
      default: true,
    },
  },

  computed: {
    ...mapGetters({
      gasPrice: 'blockchain/backendGasPrice',
      takerGasLimit: 'blockchain/tradeTakerGasLimit',
    }),

    //     (     )
    isFilled() {
      return !this.isCanceled && !this.isActive;
    },

    amountLeft() {
      let res = BigNumber(0);

      if (this.amount && this.amountFilled) {
        res = this.amount.minus(this.amountFilled);
      }

      return res;
    },

    barApproved() {
      return this.amountApproved.div(this.amount.div(100)).toFixed(0);
    },

    barPanding() {
      return this.amountPanding.div(this.amount.div(100)).toFixed(0);
    },

    createdDateString() {
      return this.createdAt ? dateToString(this.createdAt) : null;
    },

    canceledDateString() {
      return this.canceledAt ? dateToString(this.canceledAt, '') : null;
    },

    filledDateString() {
      return this.fulfilledAt ? dateToString(this.fulfilledAt, '') : null;
    },

    /**
     *     (   * )
     */
    ethAmount() {
      const { amount, unitPrice, decimals } = this;
      return amount.times(unitPrice).shiftedBy(ETH_DECIMALS - decimals);
    },

    //    ,   
    // true if amountLeft < 10 * gasFee
    isTooSmall() {
      const { decimals, amountLeft, unitPrice, gasPrice, takerGasLimit } = this;
      const avaliableAmountEth = amountLeft.times(unitPrice).shiftedBy(ETH_DECIMALS - decimals);
      const gasFeeAmountEth = gasPrice.times(takerGasLimit);
      return avaliableAmountEth.lt(gasFeeAmountEth.times(10));
    },
  },

  created() {
    Object.assign(this, {
      iconEarned,
      iconFlag,
      iconLoss,
      iconProfit,
      iconTrash,
    });
  },

  methods: {
    ...mapMutations({
      handleCancel: 'orders/CONFIRMATION_CANCEL_ORDER',
    }),
  },

  components: {
    EBigNumber,
    EIcon,
    EOrderAttentionHint,
    ETokenIcon,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-order {
  $this: &;

  box-shadow: 0 1px 3px rgba(black, .08);
  background: #fff;
  border-radius: 5px;
  margin-bottom: 9px;

  &:last-child {
    margin-bottom: 1px;
  }

  &_buy {
    #{$this}__amount-tokens {
      color: $color-green;
    }

    #{$this}__rail-fill {
      background-color: $color-green;
    }

    #{$this}__rail-fill_filled {
      opacity: 0.4;
    }

    #{$this}__hint {
      color: $color-green;
    }
  }

  &_sell {
    #{$this}__amount-tokens {
      color: $color-red;
    }

    #{$this}__rail-fill {
      background-color: $color-red;
    }

    #{$this}__rail-fill_filled {
      opacity: 0.4;
    }

    #{$this}__hint {
      color: $color-red;
    }
  }

  //  
  &_canceled {
    &:before {
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      background-image: repeating-linear-gradient(-60deg, #fff, #fff 12px, #fef8f8 12px, #fef8f8 16px)
    }

    #{$this}__condition {
      background-color: rgba(#DA4830, .38);
      border: 2px solid #DA4830;
    }
  }

  &_filled {
    #{$this}__condition {
      background-color: #baf5c6;
      border: 2px solid #1FCE43;
    }
  }

  &__top {
    border-bottom: 1px solid #EFF2FC;
    display: flex;
    justify-content: space-between;
    padding-left: 7px;
    height: 35px;

    @include r(1400) {
      height: 28px;
    }
  }

  &__total-token {
    padding-top: 7px;

    @include r(1400) {
      padding-top: 4px;
    }
  }

  &__icon-status {
    width: 12px;
    height: 11px;
    display: inline-block;
    position: relative;
    top: 1px;
    margin-right: 4px;

    @include r(1400) {
      top: 2px;
    }
  }

  &__amount-tokens {
    font-size: 12px;
    font-weight: 600;
    display: inline-block;

    @include r(1400) {
      font-size: 11px;
    }
  }

  &__price {
    font-size: 12px;
    font-weight: 600;
    display: inline-block;

    @include r(1400) {
      font-size: 11px;
    }

    .e-big-number {
      letter-spacing: .73px;
      color: $color-ultramarine;

      &__int,
      &__leading-zeros {
        color: $color-ultramarine;
        opacity: .5;
      }
    }

    #{$this}_buy & {
      color: $color-green;
    }

    #{$this}_sell & {
      color: $color-red;
    }
  }

  &__price_total {
    .e-big-number {
      color: #0123e7;

      &__int,
      &__leading-zeros {
        color: #0123e7;
        opacity: 1;
      }
    }

    #{$this}_buy & {
      color: #0123e7;
      padding-top: 5px;
    }

    #{$this}_sell & {
      color: #0123e7;
    }

  }

  &__price-icon {
    height: 12px;
    width: 12px;
    position: relative;
    top: 1px;
    left: -3px;
    display: inline-block;

    @include r(1400) {
      height: 10px;
      width: 10px;
    }

    .e-icon svg {
      fill: #CFCFDE;
    }
  }

  &__price_total &__price-icon {
    .e-icon svg {
      fill: #0123e7;
      color: #0123e7;
    }
  }

  &__shortname {
    opacity: .5;
  }

  &__timer {
    position: relative;
    z-index: 1;
    color: $color-violet-bright;
    font-size: 12px;
    position: absolute;
    top: 27px;
    left: 50%;
    transform: translateX(-50%);
    font-weight: 600;
    min-width: 220px;
    text-align: center;

    @include r(1400) {
      font-size: 11px;
      top: 24px;
    }
  }

  &__body {
    padding-top: 9px;
    padding-left: 36px;
    padding-right: 12px;
    box-sizing: border-box;
    display: flex;
    flex-direction: column;
    position: relative;
    height: 70px;

    @include r(1400) {
      height: 56px;
    }
  }

  &__token-image {
    position: absolute;
    top: 10px;
    left: 9px;
    height: 20px;
    width: 20px;
    border-radius: 50%;
    overflow: hidden;
  }

  &__delete {
    position: absolute;
    right: 0px;
    top: 6px;
    cursor: pointer;
    width: 21px;
    height: 21px;
    margin-right: 8px;
    display: inline-block;

    .e-icon svg {
      fill: $color-violet-bright;
      transition: $transition-time;
    }

    &:hover {
      .e-icon svg {
        fill: $color-blue;
      }
    }

    @include r(1400) {
      margin-right: 6px;
      width: 16px;
      height: 20px;
    }
  }

  &__delete_small-order {
    right: 20px;
  }

  &__wrapper {
    display: flex;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 1px;
    position: relative;
  }

  &__filled {
    position: relative;
    font-size: 12px;
    font-weight: 600;
    color: black;

    @include r(1400) {
      font-size: 11px;
    }

    .e-big-number {
      &__fraction,
      &__separator {
        opacity: .5;
      }
    }

    #{$this}__hint {
      left: 0px;
    }
  }

  &__left {
    position: relative;
    font-size: 12px;
    font-weight: 600;
    color: black;

    @include r(1400) {
      font-size: 11px;
    }

    .e-big-number {
      &__fraction,
      &__separator {
        opacity: .5;
      }
    }

    #{$this}__hint {
      color: $color-violet-bright;
      right: 0px;
    }
  }

  &__hint {
    position: absolute;
    bottom: -24px;
    font-size: 12px;
    font-weight: 600;

    @include r(1400) {
      font-size: 11px;
    }
  }

  &__rail {
    height: 2px;
    width: 100%;
    background-color: #EAEDFA;
    overflow: hidden;
    position: relative;
    display: flex;
    flex-direction: row;
    justify-content: flex-start;
  }

  &__rail-fill {
    border-radius: 100px 0 0 100px;
    height: 2px;
  }

  &__rail-fill_filled {
    animation: blink 1.8s infinite;
    border-radius: 0 100px 100px 0;
  }

  &__condition {
    border-radius: 50%;
    height: 8px;
    margin-right: 7px;
    margin-top: 8px;
    width: 8px;
    position: relative;
    z-index: 2;

    &:before {
      content: '';
      position: absolute;
      top: -5px;
      right: -5px;
      bottom: -5px;
      left: -5px;
      cursor: pointer;
    }

    &:hover {
      #{$this}__condition-hint {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__condition-hint {
    position: absolute;
    visibility: hidden;
    opacity: 0;
    transition: $transition-time;
    top: calc(100% + 5px);
    right: 0;
    background-color: #fff;
    color: $color-dark-blue;
    font-size: 12px;
    font-weight: 400;
    line-height: 1;
    box-shadow: 0 3px 10px rgba(148, 165, 180, 0.14);
    border-radius: 3px;
    padding: 5px 10px 5px;
    white-space: nowrap;
  }

  &__attention {
    margin-top: 9px;
    width: 25px;
    height: 16px;
    overflow: hidden;
    cursor: pointer;

    .e-icon {
      right: -5px;
    }

    .e-icon svg {
      fill: red;
      transition: $transition-time;
    }

    &:hover {
      .e-icon svg {
        fill: $color-blue;
      }
    }

    @include r(1200) {
      margin-top: 6px;
    }
  }
}

@keyframes blink {
  0%, 100% {
    opacity: 0.1;
  }
  50% {
    opacity: 0.8;
  }
}

</style>



