<template>
  <div class="the-order-form-market">
    <e-input-number
      value="MARKET PRICE"
      disabled
      :label="`Price for 1 ${activeToken.shortName}`"
      measurement-unit="ETH"
      class="the-order-form-market__input the-order-form-market__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
    />

    <e-input-number
      :value="amount"
      :decimals="activeToken.decimals"
      label="Amount"
      :measurement-unit="activeToken.shortName"
      :invalid="$v.amount.$dirty && ($v.amount.$invalid || !enoughBalance)"
      class="the-order-form-market__input the-order-form-market__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
      @input="onAmountInput"
    />

    <e-input-number
      :value="total"
      disabled
      :decimals="ETH_DECIMALS"
      label="Total (approx.)"
      measurement-unit="ETH"
      class="the-order-form-market__input the-order-form-market__item"
      labelAsPlaceholder
      squareStyle
      placeholder=""
    />

    <div class="the-order-form-market__submit the-order-form-market__item">
      <slot name="submit"/>
    </div>
  </div>
</template>

<script>
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';
import { validationMixin } from 'vuelidate';
import { required, decimal } from 'vuelidate/lib/validators';

import { ETH_DECIMALS, BUY_TOKEN_ORDER, SELL_TOKEN_ORDER } from '@/util/constants';
import { maxDecimalPlaces, greaterThanZero } from '@/util/validators';

import EInputNumber from '@/components/EInputNumber';

export default {
  name: 'the-order-form-market',
  inheritAttrs: false,

  props: {
    amount: {
      type: String,
      default: '',
    },

    buyOrSell: {
      type: String,
      required: true,
      validator: v => [BUY_TOKEN_ORDER, SELL_TOKEN_ORDER].includes(v),
    },

    anyDirty: {
      type: Boolean,
      default: false,
    },
  },

  computed: {
    /**
     * ,    
     *
     *   -         ''
     */
    total() {
      let res = '';

      if (this.isTotal) {
        let orders = this.orders;
        orders = this.buyOrSell === BUY_TOKEN_ORDER ? orders.reverse() : orders;

        let amount = BigNumber(this.amount).shiftedBy(this.activeToken.decimals);
        let i = 0;
        let total = BigNumber(0);

        while (amount.gt(BigNumber(0)) && i < orders.length) {
          if (amount.gt(orders[i].amount)) {
            amount = amount.minus(orders[i].amount);
            total = orders[i].ethAmount;
          } else {
            total = total.plus(amount.shiftedBy(ETH_DECIMALS - this.activeToken.decimals).times(orders[i].unitPrice));
            amount = BigNumber(0);
            res = total;
          }
          i += 1;
        }
      }

      return this.isTotal
        ? res.shiftedBy(-ETH_DECIMALS).toFixed()
        : res;
    },

    /**
     *      
     */
    invalid() {
      return this.$v.$invalid;
    },

    /**
     *   
     */
    orderBookTotal() {
      let res = null;

      if (this.isCalculated) {
        res = BigNumber(0);
        for (let i = 0; i < this.orders.length; i += 1) {
          res = res.plus(this.orders[i].amount);
        }
      }

      return res;
    },

    /**
     *    
     */
    orders() {
      return this.buyOrSell === BUY_TOKEN_ORDER ? this.asks : this.bids;
    },

    /**
     *      
     *
     * fasle -   
     * true -  
     */
    isCalculated() {
      //   
      if (this.invalid) return false;

      //   amount   
      const amount = BigNumber(this.amount).shiftedBy(this.activeToken.decimals);
      if (amount.isNaN() || amount.isZero()) return false;

      //    
      if (!this.orders || this.orders.length === 0) return false;

      return true;
    },

    /**
     * ,       
     *
     * false -  total  
     * true -  total  
     */
    isTotal() {
      if (!this.isCalculated) return false;

      //       amount  
      const amount = BigNumber(this.amount).shiftedBy(this.activeToken.decimals);
      if (!this.orderBookTotal || this.orderBookTotal.lt(amount)) return false;

      return true;
    },

    ...mapGetters({
      activeToken: 'tokens/activeToken',
      asks: 'orderBook/getAsks',
      bids: 'orderBook/getBids',
    }),
  },

  validations: {
    amount: {
      required,
      decimal,
      greaterThanZero,
      maxDecimalPlaces(value) {
        return maxDecimalPlaces(this.$store.getters['tokens/activeToken'].decimals)(value);
      },
    },
  },

  watch: {
    invalid: {
      immediate: true,
      handler(invalid) {
        this.$emit('update:invalid', invalid);
      },
    },

    '$v.$anyDirty': function handler(anyDirty) {
      this.$emit('update:anyDirty', anyDirty);
    },

    anyDirty: {
      immediate: true,
      handler(anyDirty) {
        if (anyDirty) {
          this.$v.$reset();
        }
      },
    },
  },

  created() {
    this.onInput();
  },

  methods: {
    onInput({ amount = this.amount } = {}) {
      this.$emit('input', { amount });
    },

    onAmountInput(amount) {
      this.$v.amount.$touch();
      this.$emit('input', { amount });
    },
  },

  components: {
    EInputNumber,
  },

  mixins: [validationMixin],
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.the-order-form-market {
  display: flex;
  flex-flow: wrap;
  justify-content: space-between;

  &__item {
    height: 50px;
    margin-bottom: 12px;

    @include r(1200) {
      height: 40px;
      margin-bottom: 10px;
    }

    @include h(750) {
      height: 40px;
      margin-bottom: 10px;
    }

    @include rwh(2400, 850) {
      height: 60px;
      margin-bottom: 14px;
    }
  }

  &__input {
    width: calc(50% - 5px);
  }

  &__submit {
    width: calc(50% - 5px);
  }
}
</style>



