    height: calc(100% - 88px);

    @include rwh(2400, 1280) {
      height: calc(100% - 147px);
    }
  }

  &__sell {
    height: calc(50% - 28px);
    position: relative;
  }

  &__sell-title {
    overflow: hidden;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 4px;
    width: 28px;
    height: 100%;
    background: #f6f7f9;
  }

  &__sell-title-text {
    width: 100px;
    position: absolute;
    top: calc(50% - 65px);
    transform: rotate(-90deg) translate(-50%, -34px);
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 800;
    color: #DA4830;
  }

  &__sell-icon {
    width: 12px;
    height: 11px;
    display: inline-block;
    position: absolute;
    margin-left: 5px;
    margin-top: 2px;
    transform: rotate(90deg);
  }

  &__buy {
    height: calc(50% - 24px);
    position: relative;

    @include rwh(2400, 1280) {
      height: calc(50% - 26px);
    }
  }

  &__buy-title {
    overflow: hidden;
    position: absolute;
    z-index: 1;
    top: 0;
    left: 4px;
    width: 28px;
    height: 100%;
    background: #f6f7f9;
  }

  &__buy-title-text {
    width: 100px;
    position: absolute;
    top: calc(50% - 65px);
    transform: rotate(-90deg) translate(-50%, -34px);
    text-transform: uppercase;
    font-size: 10px;
    font-weight: 800;
    color: #1FCE43;
  }

  &__buy-icon {
    width: 12px;
    height: 11px;
    display: inline-block;
    position: absolute;
    margin-left: 5px;
    margin-top: 1px;
    transform: rotate(90deg);
  }

  &__error {
    height: calc(100% - 33px);
    width: 100%;
    padding-left: 4px;
    padding-right: 10px;
    box-sizing: border-box;

    @include r(1200) {
      height: calc(100% - 29px);
    }
    @include h(750) {
      height: calc(100% - 29px);
    }
  }

  .e-big-number {
    letter-spacing: 0.73px;
  }
}
</style>



