<template>
  <div
    class="e-token"
    @click="$emit('click', $event)"
    :class="{ 'is-active' : active, 'e-token_slide' : catalogSlide }"
  >
    <div class="e-token__header">
      <div class="e-token__shortname">
        <div class="e-token__shortname-icon">
          <e-token-icon :contract="contract"/>
        </div>
        {{shortName}}
        <div
          v-if="isLogin"
          class="e-token__star"
          :class="{ 'e-token__star_my': isMy }"
          @click.stop="$emit('toggle-my', $event)"
        >
          <e-svg-from-store icon="star"/>
          <div class="e-token__hint">
            <div class="e-token__hint-title">
              {{ isMy ? 'Remove from My Tokens' : 'Add to My Tokens' }}
            </div>
          </div>
        </div>
      </div>

      <div class="e-token__pinners">
        <div class="e-token__pinner e-token__pinner_hint-center">
          <div class="e-token__coins">
            <e-svg-from-store :icon="`coin${coinsCount}`"/>
          </div>
          <div class="e-token__hint">
            <div class="e-token__hint-title">Available Volume</div>
            <div class="e-token__total">
              <e-big-number
                :value="total"
                :decimals="decimals"
                :decimal-places="3"
              />
              <div class="e-token__total-shortname">{{shortName}}</div>
            </div>
          </div>
        </div>

        <div class="e-token__pinner e-token__pinner_link">
          <div class="e-token__pinner-icon" @click.stop="openLink(`${ETHERSCAN_URL}/token/${contract}`)">
            <e-svg-from-store icon="websiteLink"/>
          </div>
          <e-hint-adress
            title="Token Url"
            side="right"
            :adress="contract"
          />
        </div>

        <!-- BEGIN:      URL -->
        <template v-if="websiteUrl">
          <div class="e-token__pinner e-token__pinner_hint-right">
            <a
                :href="websiteUrl"
                target="_blank"
                class="e-token__website"
                :class="{
                  'e-token__website_verified': isWebsiteVerified,
                  'e-token__website_unverified': !isWebsiteVerified,
                }"
                @click.stop
            >
              <div class="e-token__pinner-icon">
                <e-svg-from-store icon="world"/>
              </div>
            </a>
            <div class="e-token__hint">
              <div class="e-token__hint-title">{{verifiedString}}</div>
              <a
                :href="websiteUrl"
                target="_blank"
                class="e-token__website"
                :class="{
                  'e-token__website_verified': isWebsiteVerified,
                  'e-token__website_unverified': !isWebsiteVerified,
                }"
                @click.stop
              >
                {{websiteUrl}}
              </a>
            </div>
          </div>
        </template>
        <!-- END:      URL -->

        <!-- BEGIN:      URL -->
        <template v-else>
          <div class="e-token__pinner e-token__pinner_hint-right">
            <div
                class="e-token__website"
                :class="{
                  'e-token__website_verified': isWebsiteVerified,
                  'e-token__website_unverified': !isWebsiteVerified,
                }"
            >
              <div class="e-token__pinner-icon">
                <e-svg-from-store icon="world"/>
              </div>
            </div>
          </div>
        </template>
        <!-- END:      URL -->
      </div>
    </div>

    <div class="e-token__name">{{ name }}</div>

    <div class="e-token__stats">
      <div class="e-token__trade">
        <div
          class="e-token__pin"
          :class="[increasing24 ? 'e-token__pin_up' : 'e-token__pin_down']"
        >
          <e-svg-from-store :icon="increasing24 ? 'profit' : 'loss'"/>
        </div>

        <!-- TODO:    ,            -->
        <div
          class="e-token__amount"
          :class="[increasing24 ? 'e-token__amount_grow_up' : 'e-token__amount_grow_down']"
        >
          <e-big-number
            :value="price"
            :decimals="ETH_DECIMALS"
            :decimalPlaces="8"
          />
          <div class="e-token__amount-icon">
            <e-svg-from-store icon="earned"/>
          </div>
        </div>

        <div
          class="e-token__diff"
          :class="[
            increasing24 ? 'e-token__diff_grow_up' : 'e-token__diff_grow_down'
          ]"
        >
          <e-big-number
            :value="priceDiff24InPercent"
            :decimals="0"
            :decimalPlaces="2"
          />%
        </div>
      </div>

      <div class="e-token__graph">
        <e-small-price-chart
          v-if="candles24h.length > 1"
          :dataSet="candles24h"
          :chartType="increasing24 ? 'increase' : 'decrease'"
        />
      </div>
    </div>
  </div>
</template>

<script>
/**
 *   ,      
 *
 * @version 1.0.2
 */
import { BigNumber } from 'bignumber.js';
import { mapGetters } from 'vuex';

// Components
import ETokenIcon from '@/components/ETokenIcon';
import EBigNumber from '@/components/EBigNumber';
import ESmallPriceChart from '@/components/ESmallPriceChart';
import ESvgFromStore from '@/components/ESvgFromStore';
import EHintAdress from '@/components/EHintAdress';


export default {
  name: 'e-token',
  inheritAttrs: false,

  props: {
    catalogSlide: {
      type: Boolean,
      default: false,
    },

    contract: {
      type: String,
      required: true,
    },

    //  
    name: {
      type: String,
      default: '',
    },

    //   
    shortName: {
      type: String,
      default: '',
    },

    decimals: {
      type: Number,
      default: 0,
    },

    //   
    total: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    // Price
    price: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    priceDiff24InPercent: {
      type: BigNumber,
      default: () => BigNumber(0),
    },

    increasing24: {
      type: Boolean,
      default: true,
    },

    //     ""
    isMy: {
      type: Boolean,
      default: false,
    },

    /**
     *      
     */
    isWebsiteVerified: {
      type: Boolean,
      default: false,
    },

    //        
    candles24h: {
      type: Array,
      default: null,
    },

    //     
    active: {
      type: Boolean,
      default: false,
    },

    websiteUrl: {
      type: String,
      default: '#',
    },
  },

  computed: {
    ...mapGetters({
      isLogin: 'wallets/isLogin',
    }),

    coinsCount() {
      if (this.total.gt(0) && this.total.lt(500000)) {
        return 1;
      } else if (this.total.gt(500000) && this.total.lt(1000000)) {
        return 2;
      }
      return 3;
    },

    /**
     *       
     */
    verifiedString() {
      return (this.isWebsiteVerified ? 'Verified Website' : 'Unverified Website');
    },
  },

  methods: {
    openLink(url) {
      const win = window.open(url, '_blank');
      if (win) {
        win.focus();
      }
    },
  },

  components: {
    ESvgFromStore,
    EBigNumber,
    ETokenIcon,
    ESmallPriceChart,
    EHintAdress,
  },
};
</script>

<style lang="scss">
@import '~@/scss/styles';

.e-token {
  $this: &;

  height: 110px;
  position: relative;
  padding: 17px 10px 15px 15px;
  border-radius: 3px;
  cursor: pointer;
  user-select: none;
  box-sizing: border-box;


  &:hover {
    &::before {
      box-shadow: 0 5px 5px rgba(black, .08);
    }
  }

  &::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: 0 1px 3px rgba(black, .08);
    opacity: 1;
    transition: $transition-time;
    border-radius: 3px;
    z-index: -1;
  }

  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    box-shadow: inset 0 0 5px rgba(#DBE1E5, 0);
    background-color: #fff;
    transition: $transition-time;
    z-index: -1;
    border-radius: 3px;
  }

  &.is-active {
    &::after {
      background-color: #F4F5F9;
      box-shadow: inset 0 0 5px rgba(#DBE1E5, 1);
    }

    &::before {
      opacity: 0;
    }
  }

  &_slide {
    transform: translate3d(0,0,0);

    &:last-child {
      margin-bottom: 0;
    }
  }

  &__graph {
    width: 100px;
    height: 22px;
    margin-top: 2px;
    margin-right: 6px;
  }

  &__shortname {
    font-size: 17px;
    line-height: 1;
    color: black;
    position: relative;
    padding-left: 40px;
    margin-bottom: 3px;

    @include r(1200) {
      max-width: 130px;
    }
  }

  &__shortname-icon {
    position: absolute;
    top: 1px;
    left: 0;
    width: 30px;
    height: 30px;
    overflow: hidden;
    border-radius: 50%;
    display: inline-block;
  }

  &__name {
    font-size: 10px;
    text-transform: uppercase;
    color: $color-grey;
    font-weight: 600;
    text-align: left;
    padding-left: 40px;
    line-height: 1;
    letter-spacing: .5px;
    margin-bottom: 2px;
    max-width: 140px;
    margin-top: -4px;
    overflow: hidden;
    min-height: 20px;
  }

  &__star {
    position: absolute;
    display: inline-block;
    top: 0;
    right: -17px;
    width: 13px;
    height: 13px;

    .e-svg-from-store__svg {
      transition: fill $transition-time;
      fill: $color-grey-second;
    }

    #{$this}__hint {
      left: 50%;
      top: calc(100% + 5px);
      transform: translateX(-50%);
      min-width: auto;
      min-height: auto;
    }

    #{$this}__hint-title {
      font-size: 12px;
      color: $color-dark-blue;
      white-space: nowrap;
    }

    &:hover {
      .e-svg-from-store__svg {
        fill: $color-blue;
      }

      #{$this}__hint {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__star_my .e-svg-from-store__svg {
    fill: #FF9800;
  }

  &__stats {
    display: flex;
    justify-content: space-between;
  }

  &__trade {
    position: relative;
    padding-left: 28px;
    margin-left: 12px;

    &:before {
      position: absolute;
      top: 2px;
      left: 1px;
      font-size: 5px;
      font-family: 'icomoon';
    }
  }

  &__diff {
    line-height: 1;
    font-size: 11px;
    min-width: 142px;
    font-weight: 600;
  }

  &__diff_grow_up {
    color: $color-green;
  }

  &__diff_grow_down {
    color: $color-red;
  }

  &__amount {
    margin-bottom: 1px;
    min-width: 142px;

    .e-big-number {
      line-height: 1;
      font-size: 14px;
      font-weight: 400;

      &__int {
        color: $color-dark-blue;
        opacity: .5;
      }

      &__fraction {
        color: $color-ultramarine;
      }
    }
  }

  &__amount-icon {
    display: inline-block;
    width: 12px;
    height: 11px;

    svg {
      fill: $color-blue;
    }
  }

  &__pin {
    position: absolute;
    left: 1px;
    height: 16px;
    width: 18px;
    top: 6px;
    transform: rotate(-90deg);
  }

  &__pin_up {
    svg {
      fill: #1FCE43;
    }
  }

  &__pin_down {
    svg {
      fill: #DA4830;;
    }
  }

  &__total {
    line-height: 1;

    .e-big-number {
      color: $color-dark-blue;
      font-size: 11px;
      font-weight: 700;
      line-height: 1;
    }
  }

  &__total-shortname {
    display: inline-block;
    color: $color-dark-blue;
    font-size: 11px;
    font-weight: 700;
  }

  &__pinners {
    display: flex;
    position: relative;
    align-items: center;
    justify-content: center;
    z-index: 1;
    margin-top: 5px;
  }

  &__pinner {
    position: relative;
    padding: 0 6px;
    box-sizing: border-box;
    display: flex;
    align-items: center;

    &.is-active {
      #{$this}__hint {
        opacity: 1;
        visibility: visible;
      }

      #{$this}__coins svg {
        fill: $color-blue;
      }

      #{$this}__pinner-icon svg {
        fill: $color-blue;
      }
    }

    &:hover {
      #{$this}__pinner-icon svg {
        fill: $color-blue;
      }

      #{$this}__hint {
        opacity: 1;
        visibility: visible;
      }

      .e-hint-adress {
        opacity: 1;
        visibility: visible;
      }
    }
  }

  &__pinner_link {
    top: 1px;

    #{$this}__pinner-icon svg {
      fill: #CFCFDE;
    }
  }

  &__pinner_hint-center {
    #{$this}__hint {
      left: 50%;
      transform: translateX(-50%);
    }
  }

  &__pinner_hint-right {
    #{$this}__hint {
      right: 0;
    }
  }

  &__pinner-icon {
    width: 19px;
    height: 19px;
    display: flex;
    align-items: center;

    svg {
      transition: $transition-time;
      transform: translate3d(0,0,0);
    }
  }

  //   
  &__website_verified &__pinner-icon svg {
      fill: #1FCE43;
  }

  //   
  &__website_unverified &__pinner-icon svg {
      fill: $color-red;
  }


  &__hint {
    position: absolute;
    top: calc(100% + 3px);
    opacity: 0;
    transition: $transition-time;
    visibility: hidden;
    background-color: #fff;
    border-radius: 3px;
    box-shadow: 0 3px 10px rgba(#94A5B4, .14);
    box-sizing: border-box;
    min-height: 38px;
    min-width: 106px;
    text-align: center;
    padding: 5px 10px 2px;
  }

  &__hint-title {
    font-size: 10px;
    font-weight: 400;
    color: #94A5B4;
    line-height: 1;
    margin-bottom: 3px;
  }

  &__header {
    display: flex;
    justify-content: space-between;
  }

  &__coins {
    height: 12px;
    width: 20px;

    svg {
      fill: #FF9800;
      transition: $transition-time;
    }
  }

  &__website {
    font-weight: 700;
    font-size: 11px;
    display: inline-block;
    text-decoration: none;
    line-height: 1;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 150px;
    color: $color-green;
    transition: $transition-time;
    position: relative;

    &:hover {
      color: $color-blue;
    }
  }

  &__website_verified {
    color: $color-green;
  }

  &__website_unverified {
    color: $color-red;
  }
}
</style>




